import axios from 'axios'
import router from '@/router'

// axios
const request = axios.create({
  baseURL: 'http://192.168.3.135:50002',
  timeout: 60000
})

let redirectingToLogin = false

let loginPromptEl = null
let loginPromptStyleEl = null
let loginPromptPromise = null

function ensureLoginPromptStyles() {
  if (loginPromptStyleEl) return
  const style = document.createElement('style')
  style.type = 'text/css'
  style.textContent = `
  .login-prompt-mask{position:fixed;inset:0;display:flex;align-items:center;justify-content:center;background:rgba(15,23,42,.45);z-index:9999;backdrop-filter:saturate(180%) blur(10px);}
  .login-prompt-card{width:min(360px,calc(100vw - 40px));border-radius:18px;background:rgba(255,255,255,.92);box-shadow:0 20px 60px rgba(15,23,42,.25);border:1px solid rgba(226,232,240,.85);overflow:hidden;transform:translateY(8px) scale(.98);opacity:0;transition:all .18s ease;}
  .login-prompt-card.show{transform:translateY(0) scale(1);opacity:1;}
  .login-prompt-top{padding:18px 18px 10px;display:flex;gap:12px;align-items:flex-start;}
  .login-prompt-icon{width:42px;height:42px;border-radius:14px;display:flex;align-items:center;justify-content:center;background:linear-gradient(135deg,rgba(59,130,246,.16),rgba(37,99,235,.10));color:#2563eb;flex-shrink:0;}
  .login-prompt-title{font-size:15px;font-weight:800;color:#0f172a;line-height:1.3;margin:1px 0 6px;}
  .login-prompt-desc{font-size:13px;color:#475569;line-height:1.6;}
  .login-prompt-actions{display:flex;gap:10px;padding:14px 18px 18px;}
  .login-prompt-btn{flex:1;height:42px;border-radius:12px;border:1px solid rgba(226,232,240,.9);font-size:13px;font-weight:800;cursor:pointer;}
  .login-prompt-btn.secondary{background:rgba(248,250,252,.9);color:#334155;}
  .login-prompt-btn.primary{border:none;background:linear-gradient(135deg,#3b82f6 0%,#2563eb 100%);color:#fff;box-shadow:0 10px 24px rgba(37,99,235,.25);}
  .login-prompt-btn:active{transform:scale(.99);}
  `
  document.head.appendChild(style)
  loginPromptStyleEl = style
}

function closeLoginPrompt() {
  if (loginPromptEl && loginPromptEl.parentNode) {
    loginPromptEl.parentNode.removeChild(loginPromptEl)
  }
  loginPromptEl = null
  loginPromptPromise = null
}

function showLoginPrompt(reason) {
  if (loginPromptPromise) return loginPromptPromise
  if (typeof document === 'undefined') return Promise.resolve(true)
  ensureLoginPromptStyles()

  loginPromptPromise = new Promise(resolve => {
    const mask = document.createElement('div')
    mask.className = 'login-prompt-mask'

    const card = document.createElement('div')
    card.className = 'login-prompt-card'

    const top = document.createElement('div')
    top.className = 'login-prompt-top'

    const icon = document.createElement('div')
    icon.className = 'login-prompt-icon'
    icon.innerHTML = '<svg viewBox="0 0 24 24" fill="none" stroke="currentColor" stroke-width="2" stroke-linecap="round" stroke-linejoin="round" style="width:22px;height:22px"><path d="M12 9v4"/><path d="M12 17h.01"/><path d="M10.29 3.86L1.82 18a2 2 0 0 0 1.71 3h16.94a2 2 0 0 0 1.71-3L13.71 3.86a2 2 0 0 0-3.42 0z"/></svg>'

    const textWrap = document.createElement('div')
    const title = document.createElement('div')
    title.className = 'login-prompt-title'
    title.textContent = ''
    const desc = document.createElement('div')
    desc.className = 'login-prompt-desc'
    desc.textContent = reason || ''
    textWrap.appendChild(title)
    textWrap.appendChild(desc)

    top.appendChild(icon)
    top.appendChild(textWrap)

    const actions = document.createElement('div')
    actions.className = 'login-prompt-actions'
    const cancelBtn = document.createElement('button')
    cancelBtn.type = 'button'
    cancelBtn.className = 'login-prompt-btn secondary'
    cancelBtn.textContent = ''
    const okBtn = document.createElement('button')
    okBtn.type = 'button'
    okBtn.className = 'login-prompt-btn primary'
    okBtn.textContent = ''

    const done = ok => {
      closeLoginPrompt()
      resolve(ok)
    }

    cancelBtn.onclick = () => done(false)
    okBtn.onclick = () => done(true)
    mask.onclick = e => {
      if (e && e.target === mask) done(false)
    }

    actions.appendChild(cancelBtn)
    actions.appendChild(okBtn)

    card.appendChild(top)
    card.appendChild(actions)
    mask.appendChild(card)
    document.body.appendChild(mask)
    loginPromptEl = mask
    window.setTimeout(() => {
      card.classList.add('show')
    }, 10)
  })

  return loginPromptPromise
}

function createNeedLoginError() {
  const e = new Error('NEED_LOGIN')
  e.needLogin = true
  return e
}

 function normalizeLoginReason(reason) {
   const r = reason ? String(reason) : ''
   if (!r) return ''
   if (r.includes('token ')) return ''
   return r
 }

function redirectToLogin(reason) {
  if (redirectingToLogin) return
  if (!router || !router.currentRoute) return
  const currentPath = router.currentRoute.path
  if (currentPath === '/login') return

  redirectingToLogin = true
  const redirect = router.currentRoute.fullPath || '/'
  showLoginPrompt(normalizeLoginReason(reason))
    .then(ok => {
      if (!ok) {
        redirectingToLogin = false
        return
      }
      return router.replace({ path: '/login', query: { redirect } }).catch(() => {})
    })
    .finally(() => {
      window.setTimeout(() => {
        redirectingToLogin = false
      }, 300)
    })
}

function handleUnauthorized(reason) {
  redirectToLogin(normalizeLoginReason(reason))
}

function getAuthHeaders() {
  const token = localStorage.getItem('token') || localStorage.getItem('satoken')
  const headers = {}
  if (token) {
    headers.satoken = token
    headers.Authorization = `Bearer ${token}`
  }
  return headers
}

// 
request.interceptors.request.use(
  config => {
    const token = localStorage.getItem('token') || localStorage.getItem('satoken')

    //  token token
    const url = config && config.url ? String(config.url) : ''
    const isLogin = url.includes('/api/user/login') || url.includes('bookkeep/api/user/login') || url.includes('/bookkeep/api/user/login')

    //  satoken token  token 
    if (!config.headers) config.headers = {}
    if (!isLogin) {
      if (token) {
        config.headers.satoken = token
      } else if (typeof config.headers.satoken !== 'undefined') {
        delete config.headers.satoken
      }
    } else if (token) {
      config.headers.satoken = token
    } else if (typeof config.headers.satoken !== 'undefined') {
      delete config.headers.satoken
    }

    //  Authorization
    if (token) {
      config.headers.Authorization = `Bearer ${token}`
    } else if (typeof config.headers.Authorization !== 'undefined') {
      delete config.headers.Authorization
    }
    return config
  },
  error => Promise.reject(error)
)

// 
request.interceptors.response.use(
  response => {
    const res = response.data
    if (res.code === 200) {
      // 
      // 1) { code, message, data }
      // 2) { code, message, token, user }
      return typeof res.data !== 'undefined' ? res.data : res
    }
    const msg = res ? (res.msg || res.message || '') : ''
    
    //  401/403 
    if (res && (res.code === 401 || res.code === 403)) {
      handleUnauthorized(msg || '')
      return Promise.reject(createNeedLoginError())
    } 
    
    //  500  token 
    if (res && res.code === 500 && msg) {
      //  token 
      if (msg.includes('token ') || msg.includes(' token') || msg.includes('token')) {
        handleUnauthorized(msg)
        return Promise.reject(createNeedLoginError())
      }
    }
    
    return Promise.reject(new Error(msg || ''))
  },
  error => {
    const status = error && error.response ? error.response.status : 0
    if (status === 401 || status === 403) {
      handleUnauthorized('')
      return Promise.reject(createNeedLoginError())
    }
    return Promise.reject(error)
  }
)

/**
 *  - 
 * @param {string} userQuestion - 
 * @param {Array} history -  [{ role: 'user'|'assistant', content: '' }]
 * @param {Function} onChunk - 
 * @param {AbortSignal} signal - 
 */
export async function financeChat(userQuestion, history = [], onChunk, signal) {
  const baseURL = request.defaults.baseURL || ''
  const url = `${String(baseURL).replace(/\/$/, '')}/bookkeep/finance/chat/stream`
  const res = await fetch(url, {
    method: 'POST',
    headers: {
      'Content-Type': 'application/json',
      ...getAuthHeaders()
    },
    body: JSON.stringify({ userQuestion, history }),
    signal
  })

  if (res.status === 401 || res.status === 403) {
    handleUnauthorized('')
    throw createNeedLoginError()
  }

  if (!res.ok) {
    const msg = await res.text().catch(() => '')
    if (msg && msg.includes('token ')) {
      handleUnauthorized(msg)
      throw createNeedLoginError()
    }
    throw new Error(msg || `(${res.status})`)
  }

  if (!res.body) {
    const text = await res.text()
    if (typeof onChunk === 'function' && text) onChunk(text)
    return text
  }

  const reader = res.body.getReader()
  const decoder = new TextDecoder('utf-8')
  let buffer = ''
  let fullText = ''
  let shouldStop = false

  let reading = true
  while (reading) {
    const { value, done } = await reader.read()
    if (done) {
      reading = false
      break
    }
    buffer += decoder.decode(value, { stream: true })

    const parts = buffer.split(/\r?\n/)
    buffer = parts.pop() || ''

    for (const line of parts) {
      if (!line) continue
      if (line.startsWith('data:')) {
        const chunk = line.slice(5).replace(/^\s/, '')
        if (chunk === '[DONE]') {
          shouldStop = true
          break
        }
        if (!chunk) continue
        fullText += chunk
        if (typeof onChunk === 'function') onChunk(chunk)
        continue
      }
    }

    if (shouldStop) break
  }

  if (!shouldStop && buffer) {
    const tailParts = buffer.split(/\r?\n/)
    for (const line of tailParts) {
      if (!line) continue
      if (!line.startsWith('data:')) continue
      const chunk = line.slice(5).replace(/^\s/, '')
      if (!chunk || chunk === '[DONE]') continue
      fullText += chunk
      if (typeof onChunk === 'function') onChunk(chunk)
    }
  }

  return fullText
}

