 function resolveHttpBaseUrl() {
   const base = request && request.defaults && request.defaults.baseURL ? String(request.defaults.baseURL) : ''
   if (base) return base
   if (typeof window !== 'undefined' && window.location && window.location.origin) return window.location.origin
   return ''
 }

 export function buildWebSocketUrl(path, params = {}) {
   const httpBase = resolveHttpBaseUrl()
   if (!httpBase) return ''

   let u
   try {
     u = new URL(httpBase)
   } catch (e) {
     try {
       u = new URL(httpBase, typeof window !== 'undefined' ? window.location.origin : undefined)
     } catch (e2) {
       return ''
     }
   }

   const wsProtocol = u.protocol === 'https:' ? 'wss:' : 'ws:'
   const wsBase = `${wsProtocol}//${u.host}`
   const full = new URL(String(path || '').startsWith('/') ? path : `/${path}`, wsBase)

   Object.keys(params || {}).forEach(k => {
     const v = params[k]
     if (v === null || typeof v === 'undefined' || v === '') return
     full.searchParams.set(k, String(v))
   })

   return full.toString()
 }

 export function buildHumanChatWsUrl(sessionId, token) {
   return buildWebSocketUrl('/bookkeep/ws/human/chat', { sessionId, token })
 }

 export function createHumanChatWebSocket(sessionId, handlers = {}) {
   const token = handlers && handlers.token ? handlers.token : (localStorage.getItem('token') || localStorage.getItem('satoken') || '')
   const url = buildHumanChatWsUrl(sessionId, token)
   if (!url) throw new Error('WebSocket URL ')

   const ws = new WebSocket(url)

   ws.onopen = evt => {
     if (handlers && typeof handlers.onOpen === 'function') handlers.onOpen(evt)
   }

   ws.onclose = evt => {
     if (handlers && typeof handlers.onClose === 'function') handlers.onClose(evt)
   }

   ws.onerror = evt => {
     if (handlers && typeof handlers.onError === 'function') handlers.onError(evt)
   }

   ws.onmessage = evt => {
     const raw = evt && typeof evt.data === 'string' ? evt.data : ''
     if (raw === 'PONG') return
     if (handlers && typeof handlers.onRawMessage === 'function') handlers.onRawMessage(raw)
     if (!raw) return
     try {
       const msg = JSON.parse(raw)
       if (handlers && typeof handlers.onMessage === 'function') handlers.onMessage(msg)
     } catch (e) {
       if (handlers && typeof handlers.onParseError === 'function') handlers.onParseError(e)
     }
   }

   return ws
 }

