
import Vue from 'vue';
import Router from 'vue-router';

import Apps from '../views/Apps.vue';
import Message from '../views/Message.vue';
import Mine from '../views/Mine.vue';
import Consult from '../views/Consult.vue';
import HumanSupport from '../views/HumanSupport.vue';
import HumanReply from '../views/HumanReply.vue';
import InvoiceReimburse from '../views/InvoiceReimburse.vue';
import Login from '../views/Login.vue';

Vue.use(Router);

const originalReplace = Router.prototype.replace;
Router.prototype.replace = function replace(location) {
  return originalReplace.call(this, location).catch(err => err);
};

export default new Router({
  mode: 'hash',
  scrollBehavior(to, from, savedPosition) {
    if (savedPosition) {
      return savedPosition;
    }
    return { x: 0, y: 0 };
  },
  routes: [
    { path: '/', redirect: '/consult' },

    { path: '/home', redirect: '/consult' },
    { path: '/apps', name: 'AppsPage', component: Apps },
    { path: '/message', name: 'MessagePage', component: Message },
    { path: '/mine', name: 'MinePage', component: Mine },
    { path: '/consult', name: 'ConsultPage', component: Consult },
    { path: '/human-support', name: 'HumanSupportPage', component: HumanSupport },
    { path: '/human-reply', name: 'HumanReplyPage', component: HumanReply },
    { path: '/invoice-reimburse', name: 'InvoiceReimbursePage', component: InvoiceReimburse },
    { path: '/login', name: 'LoginPage', component: Login },

    { path: '/expert', redirect: '/consult' },
    { path: '/info', redirect: '/message' },

    { path: '*', redirect: '/consult' }
  ]
});
