<template>
  <div class="human-support">
    <div class="topbar">
      <button class="back-btn" type="button" @click="goBack">
        <svg viewBox="0 0 24 24" fill="none" stroke="currentColor" stroke-width="2">
          <path d="M15 18l-6-6 6-6"></path>
        </svg>
      </button>
      <div class="title-wrap">
        <div class="title"></div>
        <div class="sub">
          <span class="status-dot" :class="status"></span>
          <span v-if="status === 'online'">{{ agentName }} </span>
          <span v-else> {{ queueAhead }} </span>
        </div>
      </div>
      <button class="reply-btn" type="button" @click="openReplyPage"></button>
      <button class="end-btn" type="button" @click="endSession"></button>
    </div>

    <div ref="chatArea" class="chat-area">
      <div v-if="messages.length === 0" class="welcome">

      </div>

      <div v-else class="messages">
        <div v-for="(m, idx) in messages" :key="idx" class="message" :class="m.role">
          <div class="message-inner">
            <div v-if="m.role === 'agent'" class="message-avatar"></div>
            <div class="message-content">
              <div class="message-text">{{ m.text }}</div>
              <div class="message-meta">
                <span>{{ m.time }}</span>
                <span v-if="m.role === 'user'" class="read-status" :class="{ read: m.isRead }">
                  {{ m.isRead ? '' : '' }}
                </span>
              </div>
            </div>
          </div>
        </div>

        <div v-if="sending" class="message agent">
          <div class="message-inner">
            <div class="message-avatar"></div>
            <div class="message-content">
              <div class="typing"><span></span><span></span><span></span></div>
              <div class="message-meta"></div>
            </div>
          </div>
        </div>
      </div>
    </div>

    <div class="input-section">
      <div class="input-container">
        <textarea
          ref="inputEl"
          v-model="draft"
          class="input-field"
          placeholder="..."
          rows="1"
          @keydown.enter.exact.prevent="send"
          @input="autoResize"
        ></textarea>
        <button class="send-btn" type="button" :disabled="!draft.trim() || sending" @click="send">
          <svg viewBox="0 0 24 24" fill="none" stroke="currentColor" stroke-width="2">
            <path d="M22 2L11 13"></path>
            <path d="M22 2L15 22L11 13L2 9L22 2Z"></path>
          </svg>
        </button>
      </div>
    </div>
  </div>
</template>

<script>
import { financeChatHuman, exitHumanChat, createHumanChatWebSocket, getHumanChatMessages } from '@/api/index'

export default {
  name: 'HumanSupportPage'
  ,
  data() {
    return {
      status: 'online',
      agentName: '',
      queueAhead: 2,
      draft: '',
      sending: false,
      messages: [],
      pendingUserEchoCounts: {},
      handoffDone: false,
      sessionId: null,
      websocket: null,
      wsConnected: false,
      onlineHeartbeat: null
    };
  },
  computed: {
    statusText() {
      return this.status === 'online' ? '' : '';
    }
  },
  mounted() {
    this.restoreHumanChatState()
    this.requestHandoff()
    // 
    window.addEventListener('storage', this.onStorageChange)
    // 
    window.addEventListener('beforeunload', this.clearOnlineStatus)
  },
  beforeDestroy() {
    this.clearOnlineStatus()
    window.removeEventListener('storage', this.onStorageChange)
    window.removeEventListener('beforeunload', this.clearOnlineStatus)
    this.closeWebSocket()
  },
  methods: {
    restoreHumanChatState() {
      try {
        const stateStr = sessionStorage.getItem('humanChatState');
        if (!stateStr) return;
        
        const state = JSON.parse(stateStr);
        //  5 
        if (Date.now() - state.timestamp > 5 * 60 * 1000) {
          sessionStorage.removeItem('humanChatState');
          return;
        }
        
        // 
        if (state.sessionId) {
          this.sessionId = state.sessionId;
        }
        if (state.messages && Array.isArray(state.messages)) {
          this.messages = state.messages;
        }
        
        // 
        sessionStorage.removeItem('humanChatState');
        
        this.$nextTick(this.scrollToBottom);
      } catch (e) {
        console.error('', e);
        sessionStorage.removeItem('humanChatState');
      }
    },
    recordHumanSessionId(id) {
      const n = Number(id)
      if (Number.isNaN(n) || n <= 0) return
      try {
        const raw = localStorage.getItem('humanChatSessionIds') || '[]'
        const arr = JSON.parse(raw)
        const list = Array.isArray(arr) ? arr : []
        if (!list.includes(n)) list.push(n)
        localStorage.setItem('humanChatSessionIds', JSON.stringify(list))
      } catch (e) {
        // ignore
      }
    },
    async requestHandoff() {
      const token = localStorage.getItem('token') || localStorage.getItem('satoken')
      if (!token) {
        this.$router.push({ path: '/login', query: { redirect: this.$route.fullPath || '/consult' } })
        return
      }

      if (!this.sessionId) {
        const storedSessionId = localStorage.getItem('humanChatSessionId')
        console.log(' localStorage  sessionId:', storedSessionId);
        const n2 = Number(storedSessionId)
        if (storedSessionId && !Number.isNaN(n2) && n2 > 0) {
          this.sessionId = n2
          console.log(' sessionId:', this.sessionId);
        }
      }

      const routeSessionId = this.$route && this.$route.query ? this.$route.query.sessionId : null
      console.log(' sessionId:', routeSessionId);
      if (!this.sessionId && routeSessionId) {
        const n = Number(routeSessionId)
        if (!Number.isNaN(n) && n > 0) {
          this.sessionId = n
          console.log(' sessionId:', this.sessionId);
        }
      }

      // 
      if (this.sessionId) {
        console.log('sessionId:', this.sessionId);
        localStorage.setItem('humanChatSessionId', String(this.sessionId))
        this.recordHumanSessionId(this.sessionId)
        this.handoffDone = true
        let loaded = false
        try {
          const msgs = await getHumanChatMessages(this.sessionId)
          console.log('HumanSupport :', JSON.stringify(msgs))
          // 
          const userSentIds = this.getUserSentMessageIds()
          const list = (msgs || []).map(m => {
            //  user
            //  role 
            let role = 'agent'
            if (userSentIds.has(String(m.id))) {
              role = 'user'
            } else if (m.role === 'user') {
              role = 'user'
            }
            return {
              id: m.id,
              role,
              text: m.content,
              time: this.formatTime(new Date(m.createTime || Date.now())),
              isRead: m.isRead === 1 || m.isRead === true
            }
          })
          console.log('HumanSupport :', JSON.stringify(list))
          if (list.length > 0) {
            this.messages = list
            loaded = true
          }
        } catch (e) {
          console.error('', e);
        }

        if (!loaded && this.messages.length === 0) {
          this.messages = [
            {
              role: 'agent',
              text: '',
              time: this.formatTime(new Date())
            }
          ]
        }

        this.$nextTick(this.scrollToBottom)
        this.initWebSocket()
        return
      }

      try {
        const res = await financeChatHuman(this.sessionId ? String(this.sessionId) : undefined)
        if (res && typeof res.id !== 'undefined' && res.id !== null) {
          this.sessionId = res.id
        } else if (res && typeof res.sessionId !== 'undefined' && res.sessionId !== null) {
          this.sessionId = res.sessionId
        }

        if (this.sessionId) {
          localStorage.setItem('humanChatSessionId', String(this.sessionId))
          this.recordHumanSessionId(this.sessionId)
        }

        this.handoffDone = true
        let loaded = false
        if (this.sessionId) {
          try {
            const msgs = await getHumanChatMessages(this.sessionId)
            const list = (msgs || []).map(m => ({
              role: m.role === 'user' ? 'user' : 'agent',
              text: m.content,
              time: this.formatTime(new Date(m.createTime || Date.now())),
              isRead: m.isRead === 1 || m.isRead === true
            }))
            if (list.length > 0) {
              this.messages = list
              loaded = true
            }
          } catch (e) {
            console.error('', e);
          }
        }

        if (!loaded) {
          this.messages = [
            {
              role: 'agent',
              text: '',
              time: this.formatTime(new Date())
            }
          ]
        }

        this.$nextTick(this.scrollToBottom)

        if (this.sessionId) {
          this.initWebSocket()
        }
      } catch (e) {
        this.handoffDone = false
        this.messages = [
          {
            role: 'agent',
            text: `${(e && e.message) || ''}`,
            time: this.formatTime(new Date())
          }
        ]
      }
    },
    initWebSocket() {
      if (!this.sessionId) return
      if (this.websocket) {
        this.websocket.close()
        this.websocket = null
      }

      this.websocket = createHumanChatWebSocket(this.sessionId, {
        onOpen: () => {
          this.wsConnected = true
          // 
          this.startOnlineHeartbeat()
        },
        onClose: () => {
          this.wsConnected = false
        },
        onError: () => {
          this.wsConnected = false
        },
        onMessage: (msg) => {
          if (!msg) return
          if (msg.type === 'message' && msg.data) {
            const data = msg.data
            const serverRole = data.role
            let role = serverRole === 'user' ? 'user' : 'agent'

            // 
            if (serverRole === 'user') {
              const text = String(data.content || '')
              const counts = this.pendingUserEchoCounts || {}
              if (counts[text] > 0) {
                counts[text] = counts[text] - 1
                if (counts[text] <= 0) delete counts[text]
                this.pendingUserEchoCounts = { ...counts }
                // 
                if (data.id) {
                  this.recordUserSentMessage(data.id)
                }
                return
              }
              // 
              role = 'agent'
            }
            
            // 
            if (role === 'agent') {
              this.messages.forEach(m => {
                if (m.role === 'user') {
                  m.isRead = true
                }
              })
            }
            
            this.messages.push({
              role,
              text: data.content,
              time: this.formatTime(new Date(data.createTime || Date.now())),
              isRead: false
            })
            this.$nextTick(this.scrollToBottom)
            return
          }
          if (msg.type === 'error') {
            this.messages.push({
              role: 'agent',
              text: (msg.message || ''),
              time: this.formatTime(new Date()),
              isRead: false
            })
            this.$nextTick(this.scrollToBottom)
          }
        }
      })
    },
    closeWebSocket() {
      if (this.websocket) {
        this.websocket.close()
        this.websocket = null
      }
      this.wsConnected = false
    },
    // 
    setOnlineStatus() {
      if (!this.sessionId) return
      try {
        localStorage.setItem(`humanChat_user_online_${this.sessionId}`, String(Date.now()))
      } catch (e) {
        // ignore
      }
    },
    // 
    startOnlineHeartbeat() {
      this.stopOnlineHeartbeat()
      this.setOnlineStatus()
      this.onlineHeartbeat = setInterval(() => {
        this.setOnlineStatus()
      }, 2000) // 2
    },
    // 
    stopOnlineHeartbeat() {
      if (this.onlineHeartbeat) {
        clearInterval(this.onlineHeartbeat)
        this.onlineHeartbeat = null
      }
    },
    // 
    clearOnlineStatus() {
      this.stopOnlineHeartbeat()
      if (!this.sessionId) return
      try {
        localStorage.removeItem(`humanChat_user_online_${this.sessionId}`)
      } catch (e) {
        // ignore
      }
    },
    // 
    isAdminOnline() {
      if (!this.sessionId) return false
      try {
        const ts = localStorage.getItem(`humanChat_admin_online_${this.sessionId}`)
        if (!ts) return false
        // 5
        return Date.now() - Number(ts) < 5000
      } catch (e) {
        return false
      }
    },
    //  storage 
    onStorageChange(e) {
      if (!this.sessionId) return
      const key = `humanChat_admin_online_${this.sessionId}`
      if (e.key === key && e.newValue) {
        // 
        this.messages.forEach(m => {
          if (m.role === 'user' && !m.isRead) {
            m.isRead = true
          }
        })
      }
    },
    // ID
    getUserSentMessageIds() {
      try {
        const key = `humanChat_user_sent_${this.sessionId}`
        const raw = localStorage.getItem(key) || '[]'
        return new Set(JSON.parse(raw))
      } catch (e) {
        return new Set()
      }
    },
    // ID
    recordUserSentMessage(msgId) {
      if (!msgId || !this.sessionId) return
      try {
        const key = `humanChat_user_sent_${this.sessionId}`
        const raw = localStorage.getItem(key) || '[]'
        const arr = JSON.parse(raw)
        if (!arr.includes(String(msgId))) {
          arr.push(String(msgId))
          localStorage.setItem(key, JSON.stringify(arr))
        }
      } catch (e) {
        // ignore
      }
    },
    navigateBackToAi() {
      const redirect = this.$route && this.$route.query && this.$route.query.redirect ? String(this.$route.query.redirect) : '/consult'
      this.$router.replace(redirect).catch(() => {
        this.$router.replace('/consult').catch(() => {})
      })
    },
    async goBack() {
      // 
      try {
        const state = {
          sessionId: this.sessionId,
          messages: this.messages,
          timestamp: Date.now()
        };
        sessionStorage.setItem('humanChatState', JSON.stringify(state));
      } catch (e) {
        console.error('', e);
      }
      
      //  sessionId  localStorage
      if (this.sessionId) {
        try {
          localStorage.setItem('humanChatSessionId', String(this.sessionId));
        } catch (e) {
          console.error(' sessionId ', e);
        }
      }
      
      this.closeWebSocket()
      this.navigateBackToAi()
    },
    async endSession() {
      if (!this.sessionId) {
        alert('ID');
        return;
      }
      
      try {
        await exitHumanChat(String(this.sessionId))
        
        // 
        try {
          sessionStorage.removeItem('humanChatState')
          localStorage.removeItem('humanChatSessionId')
        } catch (e) {
          console.error('', e)
        }
        
        this.closeWebSocket()
        this.handoffDone = false
        this.sessionId = null
        this.navigateBackToAi()
      } catch (e) {
        alert('' + (e.message || ''));
      }
    },
    openReplyPage() {
      if (!this.sessionId) {
        alert('ID sessionId: ' + this.sessionId)
        return
      }
      
      console.log('sessionId:', this.sessionId);
      
      const token = localStorage.getItem('token') || localStorage.getItem('satoken') || ''
      const routeData = this.$router.resolve({
        path: '/human-reply',
        query: {
          sessionId: String(this.sessionId),
          token,
          autoConnect: '1'
        }
      })
      const url = routeData && routeData.href ? `${window.location.origin}${routeData.href}` : `${window.location.origin}/#/human-reply?sessionId=${this.sessionId}&token=${encodeURIComponent(token)}&autoConnect=1`
      
      console.log(' URL:', url);
      window.open(url, '_blank')
    },
    toggleStatus() {
      if (this.status === 'online') {
        this.status = 'queue';
        this.queueAhead = 3;
      } else {
        this.status = 'online';
        this.queueAhead = 0;
      }
    },
    prefill(text) {
      this.draft = text;
      this.$nextTick(() => {
        this.autoResize();
        const el = this.$refs.inputEl;
        if (el && typeof el.focus === 'function') el.focus();
      });
    },
    autoResize() {
      const el = this.$refs.inputEl;
      if (!el) return;
      el.style.height = 'auto';
      el.style.height = Math.min(el.scrollHeight, 120) + 'px';
    },
    send() {
      if (!this.draft.trim() || this.sending) return;
      const text = this.draft.trim();
      this.draft = '';
      this.$nextTick(() => {
        if (this.$refs.inputEl) this.$refs.inputEl.style.height = 'auto';
      });

      // 
      this.messages.forEach(m => {
        if (m.role === 'agent') {
          m.isRead = true
        }
      })

      // 
      const adminOnline = this.isAdminOnline()

      this.messages.push({ role: 'user', text, time: this.formatTime(new Date()), isRead: adminOnline });
      this.$nextTick(this.scrollToBottom);

      const counts = this.pendingUserEchoCounts || {}
      counts[text] = (counts[text] || 0) + 1
      this.pendingUserEchoCounts = { ...counts }

      if (this.websocket && this.websocket.readyState === WebSocket.OPEN) {
        this.websocket.send(JSON.stringify({ content: text }))
        return
      }

      if (this.handoffDone && this.sessionId) {
        this.initWebSocket()
      }

      if (!this.handoffDone) {
        this.messages.push({
          role: 'agent',
          text: '',
          time: this.formatTime(new Date()),
          isRead: false
        });
        this.$nextTick(this.scrollToBottom);
      }
    },
    formatTime(date) {
      const hh = String(date.getHours()).padStart(2, '0');
      const mm = String(date.getMinutes()).padStart(2, '0');
      return `${hh}:${mm}`;
    },
    scrollToBottom() {
      const el = this.$refs.chatArea;
      if (!el) return;
      el.scrollTop = el.scrollHeight;
    }
  }
};
</script>

