  border: 1px solid rgba(59, 130, 246, 0.6);
  background: rgba(59, 130, 246, 0.08);
  color: #2563eb;
  font-size: 12px;
  font-weight: 700;
  cursor: pointer;
  flex-shrink: 0;
  font-family: inherit;
}

.code-btn:disabled {
  opacity: 0.6;
