}

.footer {
  margin-top: auto;
  padding: 18px 16px 24px;
  padding-bottom: calc(76px + 18px);
}

.primary-btn {
  width: 100%;
  height: 52px;
  border-radius: 999px;
  border: none;
  background: linear-gradient(135deg, #3b82f6 0%, #2563eb 100%);
  color: #fff;
  font-size: 16px;
  font-weight: 800;
  cursor: pointer;
  font-family: inherit;
  box-shadow: 0 10px 30px rgba(37, 99, 235, 0.25);
}

