<script>
import { triggerRiskCheck as apiTriggerRiskCheck } from '@/api/index'

export default {
  name: 'MinePage',
  data() {
    return {
      userName: '',
      accountMasked: '187****7153',
      enterpriseName: '',
      enterpriseVerified: false,
      riskChecking: false
    };
  },
  methods: {
    goToAllApps() {
      this.$router.replace('/apps');
    },
    goToLogin() {
      this.$router.push('/login');
    },
    openFeature(name) {
      window.alert(`${name} `);
    },
    async triggerRiskCheck() {
      if (this.riskChecking) return
      
      // ID
      const userStr = localStorage.getItem('user')
      let userId = null
      if (userStr) {
        try {
          const user = JSON.parse(userStr)
          userId = user.id || user.userId
        } catch (e) {
          // ignore
        }
      }
      
      if (!userId) {
        this.showRiskResult('error', '')
        return
      }
      
      this.riskChecking = true
      
      try {
        await apiTriggerRiskCheck(userId)
        this.showRiskResult('success', '')
      } catch (e) {
        const msg = e && e.message ? e.message : ''
        if (e && e.needLogin) {
          return
        }
        this.showRiskResult('error', msg)
      } finally {
        this.riskChecking = false
      }
    },
    showRiskResult(type, message) {
      // 
      const mask = document.createElement('div')
      mask.className = 'risk-result-mask'
      
      const card = document.createElement('div')
      card.className = 'risk-result-card'
      
      const icon = document.createElement('div')
      icon.className = `risk-result-icon ${type}`
      if (type === 'success') {
        icon.innerHTML = '<svg viewBox="0 0 24 24" fill="none" stroke="currentColor" stroke-width="2"><path d="M22 11.08V12a10 10 0 1 1-5.93-9.14"></path><polyline points="22 4 12 14.01 9 11.01"></polyline></svg>'
      } else {
        icon.innerHTML = '<svg viewBox="0 0 24 24" fill="none" stroke="currentColor" stroke-width="2"><circle cx="12" cy="12" r="10"></circle><line x1="12" y1="8" x2="12" y2="12"></line><line x1="12" y1="16" x2="12.01" y2="16"></line></svg>'
      }
      
      const text = document.createElement('div')
      text.className = 'risk-result-text'
      text.textContent = message
      
      const btn = document.createElement('button')
      btn.className = 'risk-result-btn'
      btn.textContent = ''
      btn.onclick = () => {
        card.classList.remove('show')
        setTimeout(() => {
          if (mask.parentNode) mask.parentNode.removeChild(mask)
        }, 200)
      }
      
      card.appendChild(icon)
      card.appendChild(text)
      card.appendChild(btn)
      mask.appendChild(card)
      document.body.appendChild(mask)
      
      // 
      if (!document.getElementById('risk-result-styles')) {
        const style = document.createElement('style')
        style.id = 'risk-result-styles'
        style.textContent = `
          .risk-result-mask{position:fixed;inset:0;display:flex;align-items:center;justify-content:center;background:rgba(15,23,42,.5);z-index:9999;backdrop-filter:blur(8px);}
          .risk-result-card{width:min(300px,calc(100vw - 48px));background:#fff;border-radius:20px;padding:32px 24px;text-align:center;transform:scale(.9);opacity:0;transition:all .25s cubic-bezier(.4,0,.2,1);}
          .risk-result-card.show{transform:scale(1);opacity:1;}
          .risk-result-icon{width:64px;height:64px;margin:0 auto 16px;border-radius:50%;display:flex;align-items:center;justify-content:center;}
          .risk-result-icon.success{background:linear-gradient(135deg,rgba(34,197,94,.15),rgba(22,163,74,.1));color:#16a34a;}
          .risk-result-icon.error{background:linear-gradient(135deg,rgba(239,68,68,.15),rgba(220,38,38,.1));color:#dc2626;}
          .risk-result-icon svg{width:32px;height:32px;}
          .risk-result-text{font-size:15px;font-weight:600;color:#1e293b;line-height:1.5;margin-bottom:24px;}
          .risk-result-btn{width:100%;height:44px;border:none;border-radius:12px;background:linear-gradient(135deg,#3b82f6,#2563eb);color:#fff;font-size:14px;font-weight:600;cursor:pointer;}
          .risk-result-btn:active{opacity:.9;}
        `
        document.head.appendChild(style)
      }
      
      setTimeout(() => card.classList.add('show'), 10)
    }
  }
};
</script>

