<template>
  <div class="consult">
    <!--  -->
    <div class="drawer-mask" v-if="drawerOpen" @click="drawerOpen = false"></div>
    <div class="drawer" :class="{ open: drawerOpen }">
      <div class="drawer-header">
        <span></span>
        <button class="drawer-close" @click="drawerOpen = false">
          <svg viewBox="0 0 24 24" fill="none" stroke="currentColor" stroke-width="2">
            <path d="M18 6L6 18M6 6l12 12"></path>
          </svg>
        </button>
      </div>
      <div class="drawer-content">
        <div 
          v-for="(chat, idx) in chatHistory" 
          :key="chat.id || idx" 
          class="history-item"
          :class="{ active: chat.id === currentSessionId }"
          @click="loadChat(idx)"
        >
          <svg viewBox="0 0 24 24" fill="none" stroke="currentColor" stroke-width="1.5">
            <path d="M21 15a2 2 0 0 1-2 2H7l-4 4V5a2 2 0 0 1 2-2h14a2 2 0 0 1 2 2z"></path>
          </svg>
          <div class="history-text">
            <div class="history-title">{{ chat.title }}</div>
            <div class="history-time">{{ chat.time }}</div>
          </div>
          <button class="delete-btn" @click="removeChat(idx, $event)">
            <svg viewBox="0 0 24 24" fill="none" stroke="currentColor" stroke-width="2">
              <path d="M18 6L6 18M6 6l12 12"></path>
            </svg>
          </button>
        </div>
        <div v-if="!chatHistory.length" class="empty-history"></div>
      </div>
      <div class="drawer-footer">
        <button class="new-chat-btn" @click="startNewChat">
          <svg viewBox="0 0 24 24" fill="none" stroke="currentColor" stroke-width="2">
            <path d="M12 5v14M5 12h14"></path>
          </svg>
          
        </button>
      </div>
    </div>

    <!--  -->
    <div class="topbar">
      <button class="menu-btn" @click="drawerOpen = true">
        <svg viewBox="0 0 24 24" fill="none" stroke="currentColor" stroke-width="2">
          <path d="M3 12h18M3 6h18M3 18h18"></path>
        </svg>
      </button> 
      <div class="enterprise-chip" @click="switchEnterprise">
        <div class="enterprise-dot"></div>
        <span>{{ currentEnterprise }}</span>
        <svg viewBox="0 0 24 24" fill="none" stroke="currentColor" stroke-width="2">
          <path d="M6 9l6 6 6-6"></path>
        </svg>
      </div>
      <button class="new-btn" @click="startNewChat">
        <svg viewBox="0 0 24 24" fill="none" stroke="currentColor" stroke-width="2">
          <path d="M12 5v14M5 12h14"></path>
        </svg>
      </button>
    </div>

    <!--  -->
    <div ref="chatArea" class="chat-area">
      <!--  -  -->
      <div v-if="messages.length === 0" class="welcome">
        <div class="welcome-icon">
          <svg viewBox="0 0 24 24" fill="none" stroke="currentColor" stroke-width="1.2">
            <circle cx="12" cy="12" r="10"></circle>
            <path d="M8 14s1.5 2 4 2 4-2 4-2"></path>
            <line x1="9" y1="9" x2="9.01" y2="9"></line>
            <line x1="15" y1="9" x2="15.01" y2="9"></line>
          </svg>
        </div>
        <h1 class="welcome-title">AI</h1>
        <p class="welcome-desc"></p>
        
        <!--  -->
        <div class="suggest-grid">
          <button v-for="(q, i) in suggestQuestions" :key="i" class="suggest-card" @click="quickAsk(q.text)">
            <div class="suggest-icon">
              <svg v-if="q.icon === 'tax'" viewBox="0 0 24 24" fill="none" stroke="currentColor" stroke-width="1.5">
                <path d="M9 5H7a2 2 0 0 0-2 2v12a2 2 0 0 0 2 2h10a2 2 0 0 0 2-2V7a2 2 0 0 0-2-2h-2"></path>
                <rect x="9" y="3" width="6" height="4" rx="1"></rect>
              </svg>
              <svg v-else-if="q.icon === 'chart'" viewBox="0 0 24 24" fill="none" stroke="currentColor" stroke-width="1.5">
                <path d="M3 3v18h18"></path>
                <path d="M7 16l4-4 4 4 5-6"></path>
              </svg>
              <svg v-else-if="q.icon === 'alert'" viewBox="0 0 24 24" fill="none" stroke="currentColor" stroke-width="1.5">
                <path d="M10.29 3.86L1.82 18a2 2 0 0 0 1.71 3h16.94a2 2 0 0 0 1.71-3L13.71 3.86a2 2 0 0 0-3.42 0z"></path>
                <line x1="12" y1="9" x2="12" y2="13"></line>
                <line x1="12" y1="17" x2="12.01" y2="17"></line>
              </svg>
              <svg v-else viewBox="0 0 24 24" fill="none" stroke="currentColor" stroke-width="1.5">
                <circle cx="12" cy="12" r="10"></circle>
                <path d="M9.09 9a3 3 0 0 1 5.83 1c0 2-3 3-3 3"></path>
                <line x1="12" y1="17" x2="12.01" y2="17"></line>
              </svg>
            </div>
            <span>{{ q.text }}</span>
          </button>
        </div>
      </div>

      <!--  -->
      <div v-else class="messages">
        <div v-for="(m, idx) in messages" :key="'msg-' + idx + '-' + refreshCounter" class="message" :class="m.role">
          <div class="message-inner">
            <div v-if="m.role === 'assistant'" class="message-avatar">
              <svg viewBox="0 0 24 24" fill="none" stroke="currentColor" stroke-width="1.5">
                <circle cx="12" cy="12" r="10"></circle>
                <path d="M8 14s1.5 2 4 2 4-2 4-2"></path>
                <line x1="9" y1="9" x2="9.01" y2="9"></line>
                <line x1="15" y1="9" x2="15.01" y2="9"></line>
              </svg>
            </div>
            <div class="message-content">
              <div v-if="m.role === 'assistant'" class="message-text markdown-body">
                <div v-if="loading && idx === messages.length - 1 && !m.text" class="typing"><span></span><span></span><span></span></div>
                <div v-else v-html="m.renderedHtml || renderMarkdown(m.text)"></div>
                <button v-if="m.text" class="copy-btn" @click="copyMessage(m.text)">
                  <svg viewBox="0 0 24 24" fill="none" stroke="currentColor" stroke-width="2">
                    <rect x="9" y="9" width="13" height="13" rx="2" ry="2"></rect>
                    <path d="M5 15H4a2 2 0 0 1-2-2V4a2 2 0 0 1 2-2h9a2 2 0 0 1 2 2v1"></path>
                  </svg>
                </button>
              </div>
              <div v-else class="message-text">{{ m.text }}</div>
            </div>
          </div>
        </div>
      </div>
    </div>

    <!--  -->
    <div class="input-section">
      <div class="input-actions">
        <button class="action-btn" type="button" @click="goHumanSupport"></button>
        <button class="action-btn" type="button" @click="goInvoiceReimburse"></button>
      </div>
      <div class="input-container">
        <button class="add-btn" @click="triggerUpload">
          <svg viewBox="0 0 24 24" fill="none" stroke="currentColor" stroke-width="2">
            <path d="M12 5v14M5 12h14"></path>
          </svg>
        </button>
        <input ref="cameraInput" type="file" accept="image/*" capture="environment" @change="handleFileUpload" style="display:none" />
        <input ref="albumInput" type="file" accept="image/*" @change="handleFileUpload" style="display:none" />
        <input ref="invoiceInput" type="file" accept="image/*,.pdf" @change="handleFileUpload" style="display:none" />
        <textarea
          ref="inputEl"
          v-model="draft"
          class="input-field"
          placeholder="..."
          rows="1"
          @keydown.enter.exact.prevent="handleEnter"
          @input="autoResize"
        ></textarea>
        <button v-if="loading" class="stop-btn" @click="stopGeneration">
          <svg viewBox="0 0 24 24" fill="currentColor">
            <rect x="6" y="6" width="12" height="12" rx="2"></rect>
          </svg>
        </button>
        <button v-else class="send-btn" :disabled="!draft.trim()" @click="send">
          <svg viewBox="0 0 24 24" fill="none" stroke="currentColor" stroke-width="2">
            <path d="M22 2L11 13"></path>
            <path d="M22 2L15 22L11 13L2 9L22 2Z"></path>
          </svg>
        </button>
      </div>
    </div>

    <!--  -->
    <div class="modal-mask" v-if="showModal" @click.self="showModal = false">
      <div class="modal">
        <div class="modal-title"></div>
        <div class="modal-body" v-if="invoiceResult">
          <div class="result-row">
            <span class="label"></span>
            <span class="value">{{ invoiceResult.invoiceType }}</span>
          </div>
          <div class="result-row">
            <span class="label"></span>
            <span class="value">
              <select class="type-select" v-model="selectedBusinessType">
                <option v-for="opt in businessTypeOptions" :key="opt" :value="opt">{{ opt }}</option>
              </select>
            </span>
          </div>
          <div class="core-info">
            <div class="core-label"></div>
            <div class="core-text">{{ invoiceResult.coreInfo }}</div>
          </div>
          <div class="suggestion-box">
            {{ invoiceResult.financeSuggestion }}
          </div>
        </div>
        <button class="btn-close" @click="showModal = false"></button>
        <button class="btn-confirm" @click="handleConfirm" :disabled="confirming">
          {{ confirming ? '...' : '' }}
        </button>
      </div>
    </div>

    <div class="sheet-mask" v-if="showUploadSheet" @click.self="showUploadSheet = false">
      <div class="sheet">
        <button class="sheet-item" type="button" @click="chooseUpload('camera')"></button>
        <button class="sheet-item" type="button" @click="chooseUpload('album')"></button>
        <button class="sheet-item" type="button" @click="chooseUpload('invoice')"></button>
        <button class="sheet-cancel" type="button" @click="showUploadSheet = false"></button>
      </div>
    </div>
  </div>
</template>

<script>
import { financeChat, analyzeInvoice, confirmInvoice, getChatSessions, createNewChat, getChatMessages, deleteChat, showLoginPrompt } from '@/api/index'
import { marked } from 'marked'

export default {
  name: 'ConsultPage',
  data() {
    return {
      drawerOpen: false,
      currentEnterprise: '',
      currentSessionId: null,
      draft: '',
      loading: false,
      messages: [],
      chatHistory: [],
      suggestQuestions: [
        { icon: 'tax', text: '' },
        { icon: 'chart', text: '' },
        { icon: 'alert', text: '' },
        { icon: 'help', text: '' }
      ],
      // 
      showModal: false,
      invoiceResult: null,
      fileToken: '',
      confirming: false,
      showUploadSheet: false,
      selectedBusinessType: '',
      businessTypeOptions: ['', '', '', '', ''],
      // 
      abortController: null,
      // 
      refreshCounter: 0
    };
  },
  mounted() {
    this.restoreSessionState();
    this.initAiEntryState();
    // 
    const savedDraft = sessionStorage.getItem('consultDraft');
    if (savedDraft) {
      this.draft = savedDraft;
      sessionStorage.removeItem('consultDraft');
    }
  },
  beforeDestroy() {
    this.persistAiState()
  },
  watch: {
    drawerOpen(v) {
      if (v) this.loadSessions()
    }
  },
  methods: {
    stripSystemHints(list) {
      const arr = Array.isArray(list) ? list : []
      if (arr.length === 0) return arr
      return arr.filter(m => {
        const t = m && typeof m.text === 'string' ? m.text.trim() : ''
        if (!t) return true
        if (t.includes('')) return false
        if (t.includes('')) return false
        return true
      })
    },
    restoreSessionState() {
      try {
        const stateStr = sessionStorage.getItem('aiChatState');
        if (!stateStr) return;
        
        const state = JSON.parse(stateStr);
        //  5 
        if (Date.now() - state.timestamp > 5 * 60 * 1000) {
          sessionStorage.removeItem('aiChatState');
          return;
        }
        
        // 
        if (state.sessionId) {
          this.currentSessionId = state.sessionId;
        }
        if (state.messages && Array.isArray(state.messages)) {
          this.messages = this.stripSystemHints(state.messages).map(m => {
            const msg = { ...m };
            //  assistant  HTML
            if (msg.role === 'assistant' && msg.text) {
              msg.renderedHtml = this.renderMarkdown(msg.text);
            }
            return msg;
          });
          // 
          this.refreshCounter++;
        }
        
        // 
        sessionStorage.removeItem('aiChatState');
        
        this.$nextTick(this.scrollToBottom);
      } catch (e) {
        console.error('', e);
        sessionStorage.removeItem('aiChatState');
      }
    },
    initAiEntryState() {
      let navType = ''
      try {
        const entries = typeof performance !== 'undefined' && performance.getEntriesByType ? performance.getEntriesByType('navigation') : []
        navType = entries && entries[0] && entries[0].type ? String(entries[0].type) : ''
      } catch (e) {
        navType = ''
      }

      let pendingRaw = ''
      try {
        pendingRaw = sessionStorage.getItem('pendingAiChat') || ''
      } catch (e) {
        pendingRaw = ''
      }

      if (navType === 'reload' && !pendingRaw) {
        try {
          sessionStorage.removeItem('aiChatState')
          sessionStorage.removeItem('resumeAiOnce')
        } catch (e) {
          // ignore
        }
        return
      }

      let shouldResume = false
      try {
        shouldResume = sessionStorage.getItem('resumeAiOnce') === '1'
      } catch (e) {
        shouldResume = false
      }

      if (pendingRaw || shouldResume) {
        this.restoreAiState()
        if (shouldResume) {
          try {
            sessionStorage.removeItem('resumeAiOnce')
          } catch (e) {
            // ignore
          }
        }
        this.tryResumePendingAiChat()
      }
    },
    restoreAiState() {
      try {
        const raw = sessionStorage.getItem('aiChatState')
        if (!raw) return
        const parsed = JSON.parse(raw)
        if (parsed && Array.isArray(parsed.messages)) {
          this.messages = this.stripSystemHints(parsed.messages).map(m => {
            const msg = { ...m };
            //  assistant  HTML
            if (msg.role === 'assistant' && msg.text) {
              msg.renderedHtml = this.renderMarkdown(msg.text);
            }
            return msg;
          });
          // 
          this.refreshCounter++;
        }
        if (parsed && (typeof parsed.currentSessionId === 'number' || typeof parsed.currentSessionId === 'string')) {
          this.currentSessionId = parsed.currentSessionId
        }
      } catch (e) {
        // ignore
      }
    },
    persistAiState() {
      try {
        const payload = {
          messages: this.stripSystemHints(this.messages),
          currentSessionId: this.currentSessionId
        }
        sessionStorage.setItem('aiChatState', JSON.stringify(payload))
      } catch (e) {
        // ignore
      }
    },
    async tryResumePendingAiChat() {
      const waitingText = ''
      const token = localStorage.getItem('token') || localStorage.getItem('satoken')
      if (!token) return
      if (this.loading) return
      let raw = ''
      try {
        raw = sessionStorage.getItem('pendingAiChat') || ''
      } catch (e) {
        raw = ''
      }
      if (!raw) return
      let pending
      try {
        pending = JSON.parse(raw)
      } catch (e) {
        return
      }
      if (!pending || !pending.question || !Array.isArray(pending.history)) return

      const assistantIndex = Number(pending.assistantIndex)
      if (Number.isNaN(assistantIndex) || assistantIndex < 0) return
      if (!this.messages || this.messages.length <= assistantIndex) return
      const assistantMsg = this.messages[assistantIndex]
      if (!assistantMsg || assistantMsg.role !== 'assistant') return
      if (typeof assistantMsg.text !== 'string') assistantMsg.text = ''
      if (assistantMsg.text && assistantMsg.text !== waitingText) return
      if (assistantMsg.text === waitingText) assistantMsg.text = ''
      if (assistantMsg.renderedHtml) {
        this.$set(assistantMsg, 'renderedHtml', '')
      }

      this.loading = true
      this.abortController = new AbortController()
      try {
        await financeChat(pending.question, pending.history, chunk => {
          assistantMsg.text += chunk
          this.$nextTick(this.scrollToBottom)
        }, this.abortController.signal)

        this.$set(assistantMsg, 'renderedHtml', this.renderMarkdown(assistantMsg.text))
        sessionStorage.removeItem('pendingAiChat')
        this.persistAiState()
      } catch (err) {
        if (err && err.needLogin) return
        const last = this.messages[this.messages.length - 1]
        if (last && last.role === 'assistant' && !last.text) {
          last.text = '' + (err && err.message ? err.message : '')
        }
      } finally {
        this.loading = false
        this.abortController = null
        this.$nextTick(this.scrollToBottom)
      }
    },
    // 
    async loadSessions() {
      try {
        const list = await getChatSessions();
        let humanIds = []
        try {
          const raw = localStorage.getItem('humanChatSessionIds') || '[]'
          const parsed = JSON.parse(raw)
          if (Array.isArray(parsed)) humanIds = parsed.map(v => Number(v)).filter(v => !Number.isNaN(v) && v > 0)
        } catch (e) {
          humanIds = []
        }
        this.chatHistory = (list || [])
          .filter(s => !humanIds.includes(Number(s.id)))
          .map(s => ({
            id: s.id,
            title: s.title || '',
            time: this.formatTime(s.createTime || s.updatedAt)
          }));
      } catch (e) {
        console.error('', e);
      }
    },
    formatTime(dateStr) {
      if (!dateStr) return '';
      const d = new Date(dateStr);
      const now = new Date();
      const diff = now - d;
      if (diff < 86400000) return '';
      if (diff < 172800000) return '';
      return `${d.getMonth() + 1}/${d.getDate()}`;
    },
    switchEnterprise() {
      window.alert('');
    },
    async goHumanSupport() {
      // 
      const token = localStorage.getItem('token') || localStorage.getItem('satoken');
      if (!token) {
        // 
        const shouldLogin = await showLoginPrompt('');
        if (shouldLogin) {
          this.$router.push({
            path: '/login',
            query: { redirect: '/human-support' }
          });
        }
        return;
      }
      
      // 
      try {
        const state = {
          sessionId: this.currentSessionId,
          messages: this.messages,
          timestamp: Date.now()
        };
        sessionStorage.setItem('aiChatState', JSON.stringify(state));
      } catch (e) {
        console.error('', e);
      }
      
      const storedHumanSessionId = localStorage.getItem('humanChatSessionId')
      // AIIDID
      const querySessionId = this.currentSessionId || storedHumanSessionId || ''
      this.$router.push({
        path: '/human-support',
        query: {
          ...(querySessionId ? { sessionId: querySessionId } : {}),
          redirect: this.$route && this.$route.fullPath ? this.$route.fullPath : '/consult'
        }
      });
    },
    goInvoiceReimburse() {
      this.$router.push('/invoice-reimburse');
    },
    // 
    async startNewChat() {
      // 
      const token = localStorage.getItem('token') || localStorage.getItem('satoken');
      if (!token) {
        // 
        const shouldLogin = await showLoginPrompt('');
        if (shouldLogin) {
          this.$router.push({
            path: '/login',
            query: { redirect: this.$route.fullPath }
          });
        }
        return;
      }
      
      try {
        const session = await createNewChat();
        this.currentSessionId = session.id;
        this.messages = [];
        this.drawerOpen = false;
        this.loadSessions();
      } catch (e) {
        // 
        if (e && e.needLogin) {
          return;
        }
        this.messages = [];
        this.currentSessionId = null;
        this.drawerOpen = false;
      }
    },
    // 
    async loadChat(idx) {
      const chat = this.chatHistory[idx];
      if (!chat) return;
      this.currentSessionId = chat.id;
      this.drawerOpen = false;
      try {
        const msgs = await getChatMessages(chat.id);
        this.messages = this.stripSystemHints((msgs || []).map(m => {
          const msg = {
            role: m.role === 'user' ? 'user' : 'assistant',
            text: m.content
          };
          //  assistant  HTML
          if (msg.role === 'assistant') {
            msg.renderedHtml = this.renderMarkdown(msg.text);
          }
          return msg;
        }));
        this.$nextTick(this.scrollToBottom);
      } catch (e) {
        alert('' + (e.message || ''));
      }
    },
    // 
    async removeChat(idx, e) {
      e.stopPropagation();
      const chat = this.chatHistory[idx];
      if (!chat || !confirm('')) return;
      try {
        await deleteChat(chat.id);
        this.chatHistory.splice(idx, 1);
        if (this.currentSessionId === chat.id) {
          this.messages = [];
          this.currentSessionId = null;
        }
      } catch (e) {
        alert('');
      }
    },
    quickAsk(question) {
      this.draft = question;
      this.send();
    },
    autoResize() {
      const el = this.$refs.inputEl;
      if (!el) return;
      el.style.height = 'auto';
      el.style.height = Math.min(el.scrollHeight, 120) + 'px';
    },
    // 
    triggerUpload() {
      this.showUploadSheet = true;
    },
    chooseUpload(kind) {
      this.showUploadSheet = false;
      this.$nextTick(() => {
        const refName = kind === 'camera' ? 'cameraInput' : (kind === 'album' ? 'albumInput' : 'invoiceInput');
        const el = this.$refs[refName];
        if (el && el.click) el.click();
      });
    },
    async handleFileUpload(e) {
      const file = e.target.files[0];
      if (!file) return;
      try {
        const res = await analyzeInvoice(file);
        this.invoiceResult = res;
        this.fileToken = res.fileToken;
        const bt = res && res.businessType ? String(res.businessType) : '';
        this.selectedBusinessType = bt || (this.businessTypeOptions[0] || '');
        if (bt && !this.businessTypeOptions.includes(bt)) {
          this.businessTypeOptions = [bt, ...this.businessTypeOptions];
        }
        this.showModal = true;
      } catch (err) {
        alert('' + (err.message || ''));
      }
      e.target.value = '';
    },
    async handleConfirm() {
      this.confirming = true;
      try {
        await confirmInvoice(this.fileToken, this.selectedBusinessType || (this.invoiceResult && this.invoiceResult.businessType));
        alert('');
        this.showModal = false;
      } catch (err) {
        alert('' + (err.message || ''));
      }
      this.confirming = false;
    },
    async send() {
      if (!this.draft.trim()) return;
      
      //  401 
      try {
        await getChatSessions();
      } catch (e) {
        // api 
        if (e && e.needLogin) {
          // 
          sessionStorage.setItem('consultDraft', this.draft);
          return;
        }
        // 
      }
      
      // 
      if (this.loading) {
        this.stopGeneration();
      }
      
      // ID
      if (!this.currentSessionId) {
        try {
          const session = await createNewChat();
          this.currentSessionId = session.id;
          console.log('sessionId:', this.currentSessionId);
        } catch (e) {
          console.error('', e);
          // 
          if (e && e.needLogin) {
            return;
          }
          alert('' + (e.message || ''));
          return;
        }
      }
      
      const text = this.draft.trim();
      this.draft = '';
      this.$nextTick(() => {
        if (this.$refs.inputEl) {
          this.$refs.inputEl.style.height = 'auto';
        }
      });
      
      const history = this.messages.map(m => ({
        role: m.role,
        content: m.text
      }));
      
      this.messages.push({ role: 'user', text });
      this.loading = true;
      this.$nextTick(this.scrollToBottom);
      
      // 
      this.abortController = new AbortController();
      
      try {
        const assistantMsg = { role: 'assistant', text: '' };
        this.messages.push(assistantMsg);
        this.$nextTick(this.scrollToBottom);

        await financeChat(text, history, chunk => {
          assistantMsg.text += chunk;
          this.$nextTick(this.scrollToBottom);
        }, this.abortController.signal);
        
        //  HTML
        this.$set(assistantMsg, 'renderedHtml', this.renderMarkdown(assistantMsg.text));
        this.refreshCounter++;
        this.$nextTick(this.scrollToBottom);
        
        // 
        if (this.currentSessionId) {
          await this.reloadCurrentSession();
        }
      } catch (err) {
        if (err && err.needLogin) {
          const last = this.messages[this.messages.length - 1]
          if (last && last.role === 'assistant' && !last.text) {
            last.text = ''
          }
          try {
            const payload = {
              question: text,
              history,
              assistantIndex: this.messages.length - 1
            }
            sessionStorage.setItem('pendingAiChat', JSON.stringify(payload))
            this.persistAiState()
          } catch (e) {
            // ignore
          }
          return
        }
        // 
        if (err.name === 'AbortError') {
          return;
        }
        const last = this.messages[this.messages.length - 1];
        if (last && last.role === 'assistant' && !last.text) {
          last.text = '' + (err.message || '');
        } else {
          this.messages.push({
            role: 'assistant',
            text: '' + (err.message || '')
          });
        }
      } finally {
        this.loading = false;
        this.abortController = null;
        this.$nextTick(this.scrollToBottom);
      }
    },
    async reloadCurrentSession() {
      try {
        const msgs = await getChatMessages(this.currentSessionId);
        this.messages = this.stripSystemHints((msgs || []).map(m => {
          const msg = {
            role: m.role === 'user' ? 'user' : 'assistant',
            text: m.content
          };
          //  assistant  HTML
          if (msg.role === 'assistant') {
            msg.renderedHtml = this.renderMarkdown(msg.text);
          }
          return msg;
        }));
        this.refreshCounter++;
        this.$nextTick(this.scrollToBottom);
      } catch (e) {
        console.error('', e);
      }
    },
    stopGeneration() {
      if (this.abortController) {
        this.abortController.abort();
        this.abortController = null;
      }
      this.loading = false;
    },
    handleEnter() {
      if (this.loading) {
        // 
        this.stopGeneration();
        this.$nextTick(() => {
          if (this.draft.trim()) {
            this.send();
          }
        });
      } else {
        this.send();
      }
    },
    scrollToBottom() {
      const el = this.$refs.chatArea;
      if (!el) return;
      el.scrollTop = el.scrollHeight;
    },
    renderMarkdown(text) {
      if (!text) return '';
      
      // 
      text = this.convertStructuredDataToTable(text);
      
      let html = marked(text, { breaks: true });
      
      // 
      html = html.replace(/<table>/g, '<div class="table-wrapper"><table>');
      html = html.replace(/<\/table>/g, '</table></div>');
      
      return html;
    },
    convertStructuredDataToTable(text) {
      // 
      const lines = text.split('\n');
      let consecutivePipeLines = 0;
      for (const line of lines) {
        if (line.includes('|') && line.split('|').length >= 3) {
          consecutivePipeLines++;
          if (consecutivePipeLines >= 2) {
            // 2
            return this.fixPipeTable(text);
          }
        } else if (line.trim()) {
          // 
          consecutivePipeLines = 0;
        }
      }
      
      // 
      const invoicePattern = /[:]\s*\d+.*?[:]\s*\d+.*?[:]/s;
      if (invoicePattern.test(text)) {
        return this.parseInvoiceDataToTable(text);
      }
      
      // 
      const kvPattern = /^[\u4e00-\u9fa5]+[:]\s*.+$/gm;
      const matches = text.match(kvPattern);
      if (matches && matches.length >= 5) {
        return this.parseKeyValueToTable(text);
      }
      
      return text;
    },
    fixPipeTable(text) {
      //  Markdown 
      const lines = text.split('\n');
      const result = [];
      let inTable = false;
      let headerAdded = false;
      
      for (let i = 0; i < lines.length; i++) {
        const line = lines[i];
        const trimmedLine = line.trim();
        
        // 2
        const pipeCount = (trimmedLine.match(/\|/g) || []).length;
        const isTableRow = pipeCount >= 2 && trimmedLine.includes('|');
        
        if (isTableRow) {
          // 
          if (/^\|[\s\-:|]+\|$/.test(trimmedLine)) {
            continue;
          }
          
          if (!inTable) {
            // 
            inTable = true;
            headerAdded = false;
          }
          
          // 
          let formattedLine = trimmedLine;
          if (!formattedLine.startsWith('|')) {
            formattedLine = '|' + formattedLine;
          }
          if (!formattedLine.endsWith('|')) {
            formattedLine = formattedLine + '|';
          }
          
          result.push(formattedLine);
          
          // 
          if (!headerAdded) {
            const cells = formattedLine.split('|').filter(s => s.trim());
            const separators = cells.map(() => '---');
            result.push('|' + separators.join('|') + '|');
            headerAdded = true;
          }
        } else {
          // 
          if (inTable) {
            inTable = false;
            headerAdded = false;
          }
          result.push(line);
        }
      }
      
      return result.join('\n');
    },
    parseInvoiceDataToTable(text) {
      // 
      const invoiceRegex = /[:]\s*(\d+).*?[:]\s*(\d+).*?[:]\s*([\d-]+).*?[:]\s*([^,]+).*?[:]\s*([^,]+).*?[:]\s*([\d.]+).*?[:]\s*([\d.]+).*?[:]\s*([\d.]+).*?[:]\s*([^.]+)/gs;
      
      const invoices = [];
      let match;
      while ((match = invoiceRegex.exec(text)) !== null) {
        invoices.push({
          invoiceNo: match[1],
          invoiceCode: match[2],
          date: match[3],
          buyer: match[4].trim(),
          seller: match[5].trim(),
          amount: match[6],
          tax: match[7],
          total: match[8],
          status: match[9].trim()
        });
      }
      
      if (invoices.length === 0) return text;
      
      //  Markdown 
      let table = '\n\n|  |  |  |  |  |  |  |  |  |\n';
      table += '|--------|----------|----------|--------|--------|------|------|----------|------|\n';
      
      invoices.forEach(inv => {
        table += `| ${inv.invoiceNo} | ${inv.invoiceCode} | ${inv.date} | ${inv.buyer} | ${inv.seller} | ${inv.amount} | ${inv.tax} | ${inv.total} | ${inv.status} |\n`;
      });
      
      // 
      const beforeTable = text.substring(0, text.indexOf(''));
      const afterTable = text.substring(text.lastIndexOf('') + 10);
      
      return beforeTable + table + '\n\n' + afterTable;
    },
    parseKeyValueToTable(text) {
      // 
      const lines = text.split('\n').filter(l => l.trim());
      const kvPairs = [];
      
      lines.forEach(line => {
        const match = line.match(/^([\u4e00-\u9fa5]+)[:]\s*(.+)$/);
        if (match) {
          kvPairs.push({ key: match[1], value: match[2].trim() });
        }
      });
      
      if (kvPairs.length < 5) return text;
      
      let table = '\n\n|  |  |\n|------|------|\n';
      kvPairs.forEach(pair => {
        table += `| ${pair.key} | ${pair.value} |\n`;
      });
      
      return table + '\n\n';
    },
    copyMessage(text) {
      if (!text) return;
      if (navigator.clipboard && navigator.clipboard.writeText) {
        navigator.clipboard.writeText(text).then(() => {
          this.showCopyToast();
        }).catch(() => {
          this.fallbackCopy(text);
        });
      } else {
        this.fallbackCopy(text);
      }
    },
    fallbackCopy(text) {
      const textarea = document.createElement('textarea');
      textarea.value = text;
      textarea.style.position = 'fixed';
      textarea.style.opacity = '0';
      document.body.appendChild(textarea);
      textarea.select();
      try {
        document.execCommand('copy');
        this.showCopyToast();
      } catch (e) {
        alert('');
      }
      document.body.removeChild(textarea);
    },
    showCopyToast() {
      const toast = document.createElement('div');
      toast.className = 'copy-toast';
      toast.textContent = '';
      document.body.appendChild(toast);
      setTimeout(() => {
        toast.classList.add('show');
      }, 10);
      setTimeout(() => {
        toast.classList.remove('show');
        setTimeout(() => document.body.removeChild(toast), 300);
      }, 1500);
    }
  }
};
</script>

