@keyframes bounce {
  0%, 80%, 100% { transform: scale(0.5); opacity: 0.4; }
  40% { transform: scale(1); opacity: 1; }
}

/*  */
.input-section {
  padding: 12px 16px;
  padding-bottom: 12px;
  background: #fff;
  border-top: 1px solid #f1f5f9;
  flex-shrink: 0;
  position: relative;
  z-index: 10;
}

.input-actions {
  display: flex;
  gap: 8px;
  margin-bottom: 10px;
}

.action-btn {
  padding: 6px 10px;
  font-size: 12px;
  font-weight: 600;
  color: #334155;
  background: #f8fafc;
  border: 1px solid #e2e8f0;
  border-radius: 999px;
  cursor: pointer;
  font-family: inherit;
}

.action-btn:active {
  background: #f1f5f9;
}

.input-container {
  display: flex;
  align-items: flex-end;
  gap: 8px;
  padding: 8px;
  background: #f8fafc;
  border-radius: 24px;
  border: 1px solid #e2e8f0;
}

.add-btn {
  width: 36px;
  height: 36px;
  border-radius: 50%;
  border: none;
  background: #e2e8f0;
  color: #64748b;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  flex-shrink: 0;
}

.add-btn svg {
  width: 18px;
  height: 18px;
}

.add-btn:active {
  background: #cbd5e1;
}

.input-field {
  flex: 1;
  border: none;
  outline: none;
  font-size: 14px;
  color: #1e293b;
  background: transparent;
  font-family: inherit;
  resize: none;
  line-height: 1.5;
  max-height: 120px;
  padding: 6px 0;
}

.input-field::placeholder {
  color: #94a3b8;
}

.send-btn {
  width: 40px;
  height: 40px;
  border-radius: 50%;
  border: none;
  background: linear-gradient(135deg, #3b82f6 0%, #60a5fa 100%);
  color: #fff;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  flex-shrink: 0;
  transition: opacity 0.15s;
}

.send-btn svg {
  width: 18px;
  height: 18px;
}

.send-btn:disabled {
  opacity: 0.4;
  cursor: not-allowed;
}

.send-btn:active:not(:disabled) {
  opacity: 0.85;
}

.stop-btn {
  width: 40px;
  height: 40px;
  border-radius: 50%;
  border: none;
  background: linear-gradient(135deg, #ef4444 0%, #f87171 100%);
  color: #fff;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  flex-shrink: 0;
  transition: opacity 0.15s;
  animation: pulse-stop 1.5s ease-in-out infinite;
}

.stop-btn svg {
  width: 16px;
  height: 16px;
}

.stop-btn:active {
  opacity: 0.85;
}

@keyframes pulse-stop {
  0%, 100% { box-shadow: 0 0 0 0 rgba(239, 68, 68, 0.4); }
  50% { box-shadow: 0 0 0 8px rgba(239, 68, 68, 0); }
}

/*  */
.modal-mask {
  position: fixed;
  inset: 0;
  background: rgba(0,0,0,0.5);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 300;
  padding: 20px;
}
.modal {
  width: 100%;
  max-width: 340px;
  background: #fff;
  border-radius: 16px;
  padding: 24px 20px;
}
.modal-title {
  font-size: 18px;
  font-weight: 600;
  color: #3b82f6;
  text-align: center;
  margin-bottom: 24px;
}
.modal-body {
  margin-bottom: 20px;
}
.result-row {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 16px 0;
  border-bottom: 1px solid #f1f5f9;
}
.result-row .label {
  font-size: 15px;
  color: #64748b;
}
.result-row .value {
  font-size: 15px;
  color: #1e293b;
  font-weight: 500;
}

 .type-select {
   appearance: none;
   -webkit-appearance: none;
   -moz-appearance: none;
   padding: 8px 28px 8px 10px;
   border: 1px solid #e2e8f0;
   border-radius: 10px;
   background: #fff;
   color: #1e293b;
   font-size: 14px;
   line-height: 1.2;
 }

 .type-select:focus {
   outline: none;
   border-color: #3b82f6;
   box-shadow: 0 0 0 3px rgba(59, 130, 246, 0.15);
 }
.core-info {
  padding: 16px 0;
}
.core-label {
  font-size: 15px;
  color: #64748b;
  margin-bottom: 8px;
}
.core-text {
  font-size: 14px;
  color: #1e293b;
  line-height: 1.6;
}
.suggestion-box {
  margin-top: 16px;
  padding: 16px;
  background: #f0f9ff;
  border-radius: 12px;
  font-size: 14px;
  color: #3b82f6;
  line-height: 1.6;
}
.btn-close {
  width: 100%;
  height: 48px;
  background: #f1f5f9;
  border: none;
  border-radius: 12px;
  font-size: 16px;
  font-weight: 500;
  color: #64748b;
  margin-bottom: 10px;
  cursor: pointer;
}
.btn-confirm {
  width: 100%;
  height: 48px;
  background: #3b82f6;
  border: none;
  border-radius: 12px;
  font-size: 16px;
  font-weight: 500;
  color: #fff;
  cursor: pointer;
}
.btn-confirm:disabled {
  opacity: 0.6;
}

 .sheet-mask {
   position: fixed;
   inset: 0;
   background: rgba(0, 0, 0, 0.35);
   z-index: 350;
   display: flex;
   align-items: flex-end;
   justify-content: center;
   padding: 12px;
 }

 .sheet {
   width: 100%;
   max-width: 420px;
   background: rgba(255, 255, 255, 0.98);
   border-radius: 18px;
   overflow: hidden;
   box-shadow: 0 18px 48px rgba(15, 23, 42, 0.25);
 }

 .sheet-item {
   width: 100%;
   height: 52px;
   border: none;
   background: transparent;
   font-size: 16px;
   color: #0f172a;
   cursor: pointer;
 }

 .sheet-item + .sheet-item {
   border-top: 1px solid #f1f5f9;
 }

 .sheet-item:active {
   background: #f8fafc;
 }

 .sheet-cancel {
   width: 100%;
   height: 52px;
   border: none;
   background: #f1f5f9;
   font-size: 16px;
   font-weight: 600;
   color: #334155;
   cursor: pointer;
   border-top: 8px solid rgba(241, 245, 249, 0.9);
 }

 .sheet-cancel:active {
   background: #e2e8f0;
 }
</style>
