<template>
  <div class="invoice-page">
    <!--  -->
    <header class="header">
      <button class="back-btn" @click="$router.back()">
        <svg width="24" height="24" viewBox="0 0 24 24" fill="none" stroke="#fff" stroke-width="2">
          <path d="M15 18l-6-6 6-6"/>
        </svg>
      </button>
      <h1 class="title"></h1>
      <div class="header-right"></div>
    </header>

    <!--  -->
    <div class="func-cards">
      <div class="func-card" @click="triggerUpload">
        <div class="func-text"></div>
        <div class="func-icon blue">
          <svg width="40" height="40" viewBox="0 0 24 24" fill="none" stroke="#3b82f6" stroke-width="1.5">
            <rect x="3" y="3" width="7" height="7" rx="1"/>
            <rect x="14" y="3" width="7" height="7" rx="1"/>
            <rect x="3" y="14" width="7" height="7" rx="1"/>
            <path d="M14 14h7v7h-7z" stroke-dasharray="2 2"/>
          </svg>
        </div>
        <input ref="fileInput" type="file" accept="image/*,.pdf" @change="handleFileUpload" style="display:none" />
      </div>
      <div class="func-card" @click="goFillInvoice">
        <div class="func-text"></div>
        <div class="func-icon green">
          <svg width="40" height="40" viewBox="0 0 24 24" fill="none" stroke="#22c55e" stroke-width="1.5">
            <path d="M14 2H6a2 2 0 0 0-2 2v16a2 2 0 0 0 2 2h12a2 2 0 0 0 2-2V8z"/>
            <polyline points="14 2 14 8 20 8"/>
            <path d="M12 18v-6M9 15l3 3 3-3"/>
          </svg>
        </div>
      </div>
    </div>

    <!--  -->
    <div class="tip-bar">
      <span class="tip-icon"></span>
      <span class="tip-text">AI</span>
    </div>

    <!-- Tab -->
    <div class="tab-section">
      <div class="tabs">
        <div class="tab" :class="{ active: activeTab === 'invoice' }" @click="activeTab = 'invoice'">
          
          <div class="tab-line" v-if="activeTab === 'invoice'"></div>
        </div>
        <div class="tab" :class="{ active: activeTab === 'receipt' }" @click="activeTab = 'receipt'">
          
        </div>
      </div>

      <!--  -->
      <div class="record-list" v-if="records.length">
        <div class="record-item" v-for="item in records" :key="item.id">
          <div class="record-info">
            <div class="record-title">{{ item.buyerName }}</div>
            <div class="record-sub">{{ item.invoiceNo }}  {{ item.time }}</div>
          </div>
          <div class="record-amount">{{ item.amount }}</div>
        </div>
      </div>
      <div class="empty" v-else>
        <div class="empty-text"></div>
      </div>
    </div>

    <!--  -->
    <div class="modal-mask" v-if="showModal" @click.self="showModal = false">
      <div class="modal">
        <div class="modal-title"></div>
        <div class="modal-body" v-if="result">
          <div class="result-row">
            <span class="label"></span>
            <span class="value">{{ result.invoiceType }}</span>
          </div>
          <div class="result-row">
            <span class="label"></span>
            <span class="value">{{ result.businessType }}</span>
          </div>
          <div class="core-info">
            <div class="core-label"></div>
            <div class="core-text">{{ result.coreInfo }}</div>
          </div>
          <div class="suggestion-box">
            {{ result.financeSuggestion }}
          </div>
        </div>
        <button class="btn-close" @click="showModal = false"></button>
        <button class="btn-confirm" @click="handleConfirm" :disabled="confirming">
          {{ confirming ? '...' : '' }}
        </button>
      </div>
    </div>
  </div>
</template>

<script>
import { analyzeInvoice, confirmInvoice } from '@/api/index'

export default {
  name: 'InvoiceReimbursePage',
  data() {
    return {
      activeTab: 'invoice',
      records: [],
      showModal: false,
      result: null,
      fileToken: '',
      confirmType: 'INVOICE_INPUT',
      confirming: false
    }
  },
  methods: {
    triggerUpload() {
      this.$refs.fileInput.click()
    },
    async handleFileUpload(e) {
      const file = e.target.files[0]
      if (!file) return
      try {
        const res = await analyzeInvoice(file)
        this.result = res
        this.fileToken = res.fileToken
        this.showModal = true
      } catch (err) {
        alert('' + (err.message || ''))
      }
      e.target.value = ''
    },
    async handleConfirm() {
      this.confirming = true
      try {
        await confirmInvoice(this.fileToken, this.confirmType)
        alert('')
        this.showModal = false
      } catch (err) {
        alert('' + (err.message || ''))
      }
      this.confirming = false
    },
    goFillInvoice() {
      alert('')
    }
  }
}
</script>

<style scoped>
.invoice-page {
  min-height: 100vh;
  background: linear-gradient(180deg, #3b82f6 0%, #60a5fa 50%, #f5f7fa 50%);
}
.header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 16px;
  color: #fff;
}
.back-btn {
  width: 40px;
  height: 40px;
  background: none;
  border: none;
  display: flex;
  align-items: center;
  justify-content: center;
}
.title {
  font-size: 18px;
  font-weight: 600;
  margin: 0;
}
.header-right { width: 40px; }

/*  */
.func-cards {
  display: flex;
  gap: 12px;
  padding: 0 16px;
  margin-bottom: 16px;
}
.func-card {
  flex: 1;
  background: #fff;
  border-radius: 16px;
  padding: 20px 16px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  box-shadow: 0 4px 20px rgba(0,0,0,0.08);
}
.func-text {
  font-size: 16px;
  font-weight: 600;
  color: #1e293b;
}
.func-icon {
  width: 56px;
  height: 56px;
  border-radius: 14px;
  display: flex;
  align-items: center;
  justify-content: center;
}
.func-icon.blue { background: rgba(59,130,246,0.1); }
.func-icon.green { background: rgba(34,197,94,0.1); }

/*  */
.tip-bar {
  margin: 0 16px 16px;
  padding: 12px 16px;
  background: #fff;
  border-radius: 12px;
  display: flex;
  align-items: center;
  gap: 8px;
  box-shadow: 0 2px 8px rgba(0,0,0,0.04);
}
.tip-icon { font-size: 16px; }
.tip-text { font-size: 13px; color: #64748b; }

/* Tab */
.tab-section {
  background: #fff;
  border-radius: 24px 24px 0 0;
  min-height: 400px;
  padding: 20px 16px;
}
.tabs {
  display: flex;
  gap: 24px;
  margin-bottom: 20px;
}
.tab {
  font-size: 16px;
  font-weight: 500;
  color: #94a3b8;
  position: relative;
  padding-bottom: 8px;
}
.tab.active { color: #1e293b; }
.tab-line {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 24px;
  height: 3px;
  background: #f97316;
  border-radius: 2px;
}

/*  */
.record-item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 16px 0;
  border-bottom: 1px solid #f1f5f9;
}
.record-title { font-size: 15px; color: #1e293b; font-weight: 500; }
.record-sub { font-size: 12px; color: #94a3b8; margin-top: 4px; }
.record-amount { font-size: 16px; font-weight: 600; color: #1e293b; }

/*  */
.empty {
  text-align: center;
  padding: 60px 0;
}
.empty-text { font-size: 14px; color: #94a3b8; }

/*  */
.modal-mask {
  position: fixed;
  inset: 0;
  background: rgba(0,0,0,0.5);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 100;
  padding: 20px;
}
.modal {
  width: 100%;
  max-width: 340px;
  background: #fff;
  border-radius: 16px;
  padding: 24px 20px;
}
.modal-title {
  font-size: 18px;
  font-weight: 600;
  color: #3b82f6;
  text-align: center;
  margin-bottom: 24px;
}
.modal-body {
  margin-bottom: 20px;
}
.result-row {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 16px 0;
  border-bottom: 1px solid #f1f5f9;
}
.result-row .label {
  font-size: 15px;
  color: #64748b;
}
.result-row .value {
  font-size: 15px;
  color: #1e293b;
  font-weight: 500;
}
.core-info {
  padding: 16px 0;
}
.core-label {
  font-size: 15px;
  color: #64748b;
  margin-bottom: 8px;
}
.core-text {
  font-size: 14px;
  color: #1e293b;
  line-height: 1.6;
}
.suggestion-box {
  margin-top: 16px;
  padding: 16px;
  background: #f0f9ff;
  border-radius: 12px;
  font-size: 14px;
  color: #3b82f6;
  line-height: 1.6;
}
.btn-close {
  width: 100%;
  height: 48px;
  background: #f1f5f9;
  border: none;
  border-radius: 12px;
  font-size: 16px;
  font-weight: 500;
  color: #64748b;
  margin-bottom: 10px;
}
.btn-confirm {
  width: 100%;
  height: 48px;
  background: #3b82f6;
  border: none;
  border-radius: 12px;
  font-size: 16px;
  font-weight: 500;
  color: #fff;
}
.btn-confirm:disabled {
  opacity: 0.6;
}
</style>
