<template>
  <div class="human-reply">
    <!--  -->
    <div class="drawer-mask" v-if="drawerOpen" @click="drawerOpen = false"></div>
    <div class="drawer" :class="{ open: drawerOpen }">
      <div class="drawer-header">
        <span></span>
        <button class="drawer-close" @click="drawerOpen = false">
          <svg viewBox="0 0 24 24" fill="none" stroke="currentColor" stroke-width="2">
            <path d="M18 6L6 18M6 6l12 12"></path>
          </svg>
        </button>
      </div>
      <div class="drawer-content">
        <div 
          v-for="(chat, idx) in humanChatHistory" 
          :key="chat.id || idx" 
          class="history-item"
          :class="{ active: chat.id === currentSessionId, unread: chat.unreadCount > 0 }"
          @click="loadHumanChat(chat)"
        >
          <div class="history-avatar">
            <svg viewBox="0 0 24 24" fill="none" stroke="currentColor" stroke-width="1.5">
              <path d="M20 21v-2a4 4 0 0 0-4-4H8a4 4 0 0 0-4 4v2"></path>
              <circle cx="12" cy="7" r="4"></circle>
            </svg>
          </div>
          <div class="history-text">
            <div class="history-title">{{ chat.title || '' }}</div>
            <div class="history-preview">{{ chat.lastMessage || '' }}</div>
            <div class="history-time">{{ formatSessionTime(chat.createTime || chat.updatedAt) }}</div>
          </div>
          <span v-if="chat.unreadCount > 0" class="unread-badge">{{ chat.unreadCount > 99 ? '99+' : chat.unreadCount }}</span>
        </div>
        <div v-if="!humanChatHistory.length && !loadingList" class="empty-history"></div>
        <div v-if="loadingList" class="empty-history">...</div>
      </div>
      <div class="drawer-footer">
        <button class="refresh-btn" @click="loadHumanSessions">
          <svg viewBox="0 0 24 24" fill="none" stroke="currentColor" stroke-width="2">
            <path d="M23 4v6h-6"></path>
            <path d="M1 20v-6h6"></path>
            <path d="M3.51 9a9 9 0 0 1 14.85-3.36L23 10M1 14l4.64 4.36A9 9 0 0 0 20.49 15"></path>
          </svg>
          
        </button>
      </div>
    </div>

    <div class="topbar">
      <button class="menu-btn" type="button" @click="openDrawer">
        <svg viewBox="0 0 24 24" fill="none" stroke="currentColor" stroke-width="2">
          <path d="M3 12h18M3 6h18M3 18h18"></path>
        </svg>
      </button>
      <div class="title"></div>
      <div class="status" :class="wsConnected ? 'online' : 'offline'">{{ wsConnected ? '' : '' }}</div>
    </div>

    <div ref="chatArea" class="chat-area">
      <div v-if="!currentSessionId" class="empty-state">
        <div class="empty-icon">
          <svg viewBox="0 0 24 24" fill="none" stroke="currentColor" stroke-width="1.5">
            <path d="M21 15a2 2 0 0 1-2 2H7l-4 4V5a2 2 0 0 1 2-2h14a2 2 0 0 1 2 2z"></path>
          </svg>
        </div>
        <div class="empty-text"></div>
        <button class="empty-btn" @click="openDrawer"></button>
      </div>
      <div v-else-if="messages.length === 0" class="empty"></div>
      <div v-else class="messages">
        <div v-for="(m, idx) in messages" :key="idx" class="message" :class="m.role">
          <div class="message-inner">
            <div v-if="m.role === 'user'" class="message-avatar user-avatar">
              <svg viewBox="0 0 24 24" fill="none" stroke="currentColor" stroke-width="1.5">
                <path d="M20 21v-2a4 4 0 0 0-4-4H8a4 4 0 0 0-4 4v2"></path>
                <circle cx="12" cy="7" r="4"></circle>
              </svg>
            </div>
            <div v-else-if="m.role === 'admin'" class="message-avatar admin-avatar"></div>
            <div class="message-content">
              <div class="message-text">{{ m.text }}</div>
              <div class="message-meta">
                <span>{{ m.time }}</span>
                <span v-if="m.role === 'admin'" class="read-status" :class="{ read: m.isRead }">
                  {{ m.isRead ? '' : '' }}
                </span>
                <span v-if="m.role === 'user'" class="read-status" :class="{ read: m.isRead }">
                  {{ m.isRead ? '' : '' }}
                </span>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>

    <div class="input-section">
      <div class="input-container">
        <textarea
          ref="inputEl"
          v-model="draft"
          class="input-field"
          placeholder="..."
          rows="1"
          @keydown.enter.exact.prevent="send"
          @input="autoResize"
        ></textarea>
        <button class="send-btn" type="button" :disabled="!draft.trim() || !wsConnected" @click="send">
          <svg viewBox="0 0 24 24" fill="none" stroke="currentColor" stroke-width="2">
            <path d="M22 2L11 13"></path>
            <path d="M22 2L15 22L11 13L2 9L22 2Z"></path>
          </svg>
        </button>
      </div>
    </div>
  </div>
</template>

<script>
import { createHumanChatWebSocket, getHumanChatSessions, getHumanChatMessages, financeChatRead } from '@/api/index'

export default {
  name: 'HumanReplyPage',
  data() {
    return {
      currentSessionId: null,
      token: '',
      websocket: null,
      wsConnected: false,
      draft: '',
      messages: [],
      receivedIds: new Set(),
      pendingAdminEchoCounts: {},
      // 
      drawerOpen: false,
      humanChatHistory: [],
      loadingList: false,
      onlineHeartbeat: null
    }
  },
  mounted() {
    const query = this.$route && this.$route.query ? this.$route.query : {}
    
    //  localStorage  token
    this.token = localStorage.getItem('token') || localStorage.getItem('satoken') || ''
    
    //  URL  sessionId
    if (query.sessionId) {
      const n = Number(query.sessionId)
      if (!Number.isNaN(n) && n > 0) {
        this.currentSessionId = n
      }
    }
    
    // 
    this.loadHumanSessions()
    
    //  sessionId
    if (this.currentSessionId) {
      this.$nextTick(() => {
        this.loadMessages()
        this.connect()
      })
    }
    
    // 
    window.addEventListener('storage', this.onStorageChange)
    // 
    window.addEventListener('beforeunload', this.clearOnlineStatus)
  },
  beforeDestroy() {
    this.clearOnlineStatus()
    window.removeEventListener('storage', this.onStorageChange)
    window.removeEventListener('beforeunload', this.clearOnlineStatus)
    this.disconnect()
  },
  methods: {
    // 
    async openDrawer() {
      this.drawerOpen = true
      await this.loadHumanSessions()
    },
    // 
    async loadHumanSessions() {
      this.loadingList = true
      try {
        const list = await getHumanChatSessions('HUMAN')
        this.humanChatHistory = (list || []).map(s => ({
          id: s.id,
          title: s.title || '',
          lastMessage: s.lastMessage || '',
          createTime: s.createTime || s.updatedAt,
          unreadCount: s.unreadCount || 0
        }))
      } catch (e) {
        console.error('', e)
      } finally {
        this.loadingList = false
      }
    },
    // 
    async loadHumanChat(chat) {
      if (!chat || !chat.id) return
      
      // 
      this.clearOnlineStatus()
      
      // 
      this.disconnect()
      
      this.currentSessionId = chat.id
      this.drawerOpen = false
      this.messages = []
      this.receivedIds = new Set()
      
      // 
      await this.loadMessages()
      this.connect()
    },
    // 
    async loadMessages() {
      if (!this.currentSessionId) return
      
      try {
        const msgs = await getHumanChatMessages(this.currentSessionId)
        console.log('HumanReply :', JSON.stringify(msgs))
        // 
        const adminSentIds = this.getAdminSentMessageIds()
        this.messages = (msgs || []).map(m => {
          //  admin
          //  user
          let role = 'user'
          if (adminSentIds.has(String(m.id))) {
            role = 'admin'
          } else if (m.role === 'admin' || m.role === 'agent') {
            role = 'admin'
          }
          return {
            id: m.id,
            role,
            text: m.content,
            time: this.formatTime(new Date(m.createTime || Date.now())),
            isRead: m.isRead === 1 || m.isRead === true
          }
        })
        console.log('HumanReply :', JSON.stringify(this.messages))
        // 
        this.markMessagesAsRead()
        this.$nextTick(this.scrollToBottom)
      } catch (e) {
        console.error('', e)
      }
    },
    // 
    async markMessagesAsRead() {
      const unreadMessages = this.messages.filter(m => m.role === 'user' && !m.isRead && m.id)
      for (const msg of unreadMessages) {
        try {
          await financeChatRead(msg.id)
          msg.isRead = true
        } catch (e) {
          console.error('', e)
        }
      }
      // 
      this.loadHumanSessions()
    },
    // 
    formatSessionTime(dateStr) {
      if (!dateStr) return ''
      const d = new Date(dateStr)
      const now = new Date()
      const diff = now - d
      if (diff < 86400000) return ''
      if (diff < 172800000) return ''
      return `${d.getMonth() + 1}/${d.getDate()}`
    },
    formatTime(date) {
      const hh = String(date.getHours()).padStart(2, '0')
      const mm = String(date.getMinutes()).padStart(2, '0')
      return `${hh}:${mm}`
    },
    connect() {
      if (!this.currentSessionId) {
        window.alert('')
        return
      }

      if (this.websocket) {
        this.disconnect()
      }

      try {
        this.websocket = createHumanChatWebSocket(this.currentSessionId, {
          token: this.token,
          onOpen: () => {
            this.wsConnected = true
            // 
            this.startOnlineHeartbeat()
          },
          onClose: () => {
            this.wsConnected = false
          },
          onError: () => {
            this.wsConnected = false
          },
          onMessage: (msg) => {
            if (!msg) return
            if (msg.type === 'message' && msg.data) {
              const data = msg.data
              if (data && typeof data.id !== 'undefined' && data.id !== null) {
                const idStr = String(data.id)
                if (this.receivedIds.has(idStr)) return
                this.receivedIds.add(idStr)
              }
              const serverRole = data.role
              const role = serverRole === 'user' ? 'user' : 'admin'
              
              //  role
              const text = String(data.content || '')
              const counts = this.pendingAdminEchoCounts || {}
              if (counts[text] > 0) {
                counts[text] = counts[text] - 1
                if (counts[text] <= 0) delete counts[text]
                this.pendingAdminEchoCounts = { ...counts }
                // 
                if (data.id) {
                  this.recordAdminSentMessage(data.id)
                }
                return
              }
              
              // 
              if (role === 'user') {
                this.messages.forEach(m => {
                  if (m.role === 'admin') {
                    m.isRead = true
                  }
                })
              }
              
              this.messages.push({
                id: data.id,
                role,
                text: data.content,
                time: this.formatTime(new Date(data.createTime || Date.now())),
                isRead: false
              })
              // 
              if (role === 'user' && data.id) {
                financeChatRead(data.id).catch(() => {})
              }
              this.$nextTick(this.scrollToBottom)
              return
            }
            if (msg.type === 'error') {
              console.error('WebSocket error:', msg.message)
            }
          }
        })
      } catch (e) {
        this.websocket = null
        this.wsConnected = false
        window.alert((e && e.message) || '')
      }
    },
    disconnect() {
      if (this.websocket) {
        this.websocket.close()
        this.websocket = null
      }
      this.wsConnected = false
    },
    // 
    setOnlineStatus() {
      if (!this.currentSessionId) return
      try {
        localStorage.setItem(`humanChat_admin_online_${this.currentSessionId}`, String(Date.now()))
      } catch (e) {
        // ignore
      }
    },
    // 
    startOnlineHeartbeat() {
      this.stopOnlineHeartbeat()
      this.setOnlineStatus()
      this.onlineHeartbeat = setInterval(() => {
        this.setOnlineStatus()
      }, 2000) // 2
    },
    // 
    stopOnlineHeartbeat() {
      if (this.onlineHeartbeat) {
        clearInterval(this.onlineHeartbeat)
        this.onlineHeartbeat = null
      }
    },
    // 
    clearOnlineStatus() {
      this.stopOnlineHeartbeat()
      if (!this.currentSessionId) return
      try {
        localStorage.removeItem(`humanChat_admin_online_${this.currentSessionId}`)
      } catch (e) {
        // ignore
      }
    },
    // 
    isUserOnline() {
      if (!this.currentSessionId) return false
      try {
        const ts = localStorage.getItem(`humanChat_user_online_${this.currentSessionId}`)
        if (!ts) return false
        // 5
        return Date.now() - Number(ts) < 5000
      } catch (e) {
        return false
      }
    },
    //  storage 
    onStorageChange(e) {
      if (!this.currentSessionId) return
      const key = `humanChat_user_online_${this.currentSessionId}`
      if (e.key === key && e.newValue) {
        // 
        this.messages.forEach(m => {
          if (m.role === 'admin' && !m.isRead) {
            m.isRead = true
          }
        })
      }
    },
    // ID
    getAdminSentMessageIds() {
      try {
        const key = `humanChat_admin_sent_${this.currentSessionId}`
        const raw = localStorage.getItem(key) || '[]'
        return new Set(JSON.parse(raw))
      } catch (e) {
        return new Set()
      }
    },
    // ID
    recordAdminSentMessage(msgId) {
      if (!msgId || !this.currentSessionId) return
      try {
        const key = `humanChat_admin_sent_${this.currentSessionId}`
        const raw = localStorage.getItem(key) || '[]'
        const arr = JSON.parse(raw)
        if (!arr.includes(String(msgId))) {
          arr.push(String(msgId))
          localStorage.setItem(key, JSON.stringify(arr))
        }
      } catch (e) {
        // ignore
      }
    },
    send() {
      const text = this.draft.trim()
      if (!text) return
      if (!this.wsConnected || !this.websocket) return

      // 
      this.messages.forEach(m => {
        if (m.role === 'user') {
          m.isRead = true
        }
      })

      // 
      const userOnline = this.isUserOnline()

      // 
      this.messages.push({
        id: null,
        role: 'admin',
        text: text,
        time: this.formatTime(new Date()),
        isRead: userOnline
      })
      
      // 
      const counts = this.pendingAdminEchoCounts || {}
      counts[text] = (counts[text] || 0) + 1
      this.pendingAdminEchoCounts = { ...counts }

      this.websocket.send(JSON.stringify({ content: text }))
      this.draft = ''
      this.$nextTick(() => {
        if (this.$refs.inputEl) this.$refs.inputEl.style.height = 'auto'
        this.scrollToBottom()
      })
    },
    autoResize() {
      const el = this.$refs.inputEl
      if (!el) return
      el.style.height = 'auto'
      el.style.height = Math.min(el.scrollHeight, 120) + 'px'
    },
    scrollToBottom() {
      const el = this.$refs.chatArea
      if (!el) return
      el.scrollTop = el.scrollHeight
    }
  }
}
</script>

<style scoped>
.human-reply {
  max-width: 414px;
  margin: 0 auto;
  height: 100%;
  display: flex;
  flex-direction: column;
  background: #f5f7fa;
}

/*  */
.drawer-mask {
  position: fixed;
  inset: 0;
  background: rgba(0, 0, 0, 0.4);
  z-index: 200;
}

/*  */
.drawer {
  position: fixed;
  top: 0;
  left: -300px;
  width: 300px;
  height: 100%;
  background: #fff;
  z-index: 201;
  display: flex;
  flex-direction: column;
  transition: left 0.3s ease;
  box-shadow: 4px 0 20px rgba(0, 0, 0, 0.1);
}

.drawer.open {
  left: 0;
}

.drawer-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 16px;
  border-bottom: 1px solid #f1f5f9;
  font-size: 16px;
  font-weight: 600;
  color: #1e293b;
}

.drawer-close {
  width: 32px;
  height: 32px;
  border: none;
  background: transparent;
  color: #64748b;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  border-radius: 6px;
}

.drawer-close svg {
  width: 20px;
  height: 20px;
}

.drawer-close:active {
  background: #f1f5f9;
}

.drawer-content {
  flex: 1;
  overflow-y: auto;
  padding: 8px;
}

.history-item {
  display: flex;
  align-items: center;
  gap: 10px;
  padding: 12px;
  border-radius: 10px;
  cursor: pointer;
  margin-bottom: 4px;
  position: relative;
  background: #fff;
  border: 1px solid #f1f5f9;
}

.history-item:active,
.history-item.active {
  background: #eff6ff;
  border-color: #3b82f6;
}

.history-item.unread {
  background: rgba(34, 197, 94, 0.06);
  border-color: rgba(34, 197, 94, 0.2);
}

.history-avatar {
  width: 40px;
  height: 40px;
  border-radius: 10px;
  background: linear-gradient(135deg, #3b82f6 0%, #60a5fa 100%);
  color: #fff;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-shrink: 0;
}

.history-avatar svg {
  width: 20px;
  height: 20px;
}

.history-text {
  flex: 1;
  min-width: 0;
}

.history-title {
  font-size: 14px;
  font-weight: 600;
  color: #334155;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.history-preview {
  font-size: 12px;
  color: #94a3b8;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  margin-top: 2px;
}

.history-time {
  font-size: 11px;
  color: #94a3b8;
  margin-top: 2px;
}

.unread-badge {
  min-width: 18px;
  height: 18px;
  padding: 0 5px;
  background: linear-gradient(135deg, #ef4444 0%, #f87171 100%);
  color: #fff;
  font-size: 10px;
  font-weight: 700;
  border-radius: 9px;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-shrink: 0;
}

.empty-history {
  text-align: center;
  padding: 40px 20px;
  color: #94a3b8;
  font-size: 13px;
}

.drawer-footer {
  padding: 12px;
  border-top: 1px solid #f1f5f9;
}

.refresh-btn {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 6px;
  padding: 12px;
  background: linear-gradient(135deg, #22c55e 0%, #16a34a 100%);
  border: none;
  border-radius: 10px;
  font-size: 13px;
  font-weight: 600;
  color: #fff;
  cursor: pointer;
  font-family: inherit;
}

.refresh-btn svg {
  width: 16px;
  height: 16px;
}

.refresh-btn:active {
  opacity: 0.9;
}

.topbar {
  display: flex;
  align-items: center;
  gap: 10px;
  padding: 10px 12px;
  background: #fff;
  border-bottom: 1px solid #f1f5f9;
}

.menu-btn {
  width: 36px;
  height: 36px;
  border: none;
  background: transparent;
  color: #64748b;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 8px;
  cursor: pointer;
}

.menu-btn svg {
  width: 20px;
  height: 20px;
}

.menu-btn:active {
  background: #f1f5f9;
}

.title {
  flex: 1;
  font-size: 16px;
  font-weight: 700;
  color: #1e293b;
}

.status {
  font-size: 12px;
  padding: 4px 10px;
  border-radius: 999px;
  font-weight: 600;
}

.status.online {
  color: #15803d;
  background: rgba(34, 197, 94, 0.1);
}

.status.offline {
  color: #b45309;
  background: rgba(245, 158, 11, 0.1);
}

.chat-area {
  flex: 1;
  overflow-y: auto;
  padding: 16px;
  background: #fff;
}

.empty-state {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100%;
  padding: 40px;
}

.empty-icon {
  width: 64px;
  height: 64px;
  border-radius: 50%;
  background: #f1f5f9;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-bottom: 16px;
}

.empty-icon svg {
  width: 32px;
  height: 32px;
  color: #94a3b8;
}

.empty-text {
  font-size: 14px;
  color: #64748b;
  margin-bottom: 16px;
}

.empty-btn {
  padding: 10px 20px;
  background: linear-gradient(135deg, #3b82f6 0%, #2563eb 100%);
  border: none;
  border-radius: 10px;
  color: #fff;
  font-size: 14px;
  font-weight: 600;
  cursor: pointer;
}

.empty-btn:active {
  opacity: 0.9;
}

.empty {
  color: #94a3b8;
  font-size: 13px;
  text-align: center;
  padding: 40px 0;
}

.messages {
  display: flex;
  flex-direction: column;
  gap: 16px;
}

.message {
  width: 100%;
}

.message-inner {
  display: flex;
  gap: 10px;
  max-width: 100%;
}

.message.admin {
  display: flex;
  justify-content: flex-end;
}

.message.admin .message-inner {
  flex-direction: row-reverse;
  max-width: 75%;
}

.message-avatar {
  width: 36px;
  height: 36px;
  border-radius: 10px;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-shrink: 0;
}

.user-avatar {
  background: linear-gradient(135deg, #3b82f6 0%, #60a5fa 100%);
  color: #fff;
}

.user-avatar svg {
  width: 18px;
  height: 18px;
}

.admin-avatar {
  background: linear-gradient(135deg, #22c55e 0%, #16a34a 100%);
  color: #fff;
  font-size: 12px;
  font-weight: 800;
}

.message-content {
  min-width: 0;
}

.message-text {
  font-size: 14px;
  line-height: 1.6;
  color: #334155;
  white-space: pre-wrap;
  word-break: break-word;
  padding: 12px 14px;
  border-radius: 4px 16px 16px 16px;
  background: #f8fafc;
}

.message.admin .message-text {
  background: linear-gradient(135deg, #22c55e 0%, #16a34a 100%);
  color: #fff;
  border-radius: 16px 16px 4px 16px;
}

.message-meta {
  font-size: 11px;
  color: #94a3b8;
  margin-top: 4px;
